import { Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ResultHandlerService } from 'src/app/core/services/result.handler.service';
import { Carrier411RolesService } from 'src/app/carrier411/shared/services/carrier411-roles.service';
import { ContentTypeManagementComponent } from '../../../content-type-management/content-type-management.component';
import { Carrier411AdminService } from 'src/app/carrier411/admin/services/carrier411-admin.service';
import { ContentTypeUtils } from 'src/app/carrier411/admin/utilities/content-type-utils';
import { ContentTypeAction } from 'src/app/carrier411/admin/constants/content-type-action.enum';
import { ContentTypeStatusChangeDTO } from 'src/app/carrier411/admin/interfaces/content-type-status-change.dto';
import { ContentTypeActionTakenDTO } from 'src/app/carrier411/admin/interfaces/content-type-action-taken.dto';
import { Carrier411QuestionDTO } from 'src/app/carrier411/shared/interfaces/carrier411-question.dto';
import { QuestionEditComponent } from '../../../question-edit/question-edit.component';
import { Carrier411QuestionUpdateDTO } from 'src/app/carrier411/admin/interfaces/carrier411-question-update.dto';
import { Carrier411ResultDTO } from 'src/app/carrier411/shared/interfaces/carrier411-result.dto';
import { ServiceResultDTO } from 'src/app/core/interfaces/service-result/service-result.dto';
import { WorkflowStep } from 'src/app/carrier411/shared/constants/workflow-step.enum';
import { Carrier411SubCategoryDTO } from 'src/app/carrier411/shared/interfaces/carrier411-subcategory.dto';

@Component({
  selector: 'pending-approval-management-questions',
  templateUrl: './pending-approval-management-questions.component.html',
  styleUrls: ['../pending-approval-management.component.scss']
})
export class Carrier411AdminPendingApprovalManagementQuestionsComponent extends ContentTypeManagementComponent<Carrier411QuestionDTO> implements OnInit {
  @Output()
  onConfirmData = new EventEmitter();

  @ViewChild('questionEdit')
  public questionEdit!: QuestionEditComponent;

  @Input()
  public questions!: ServiceResultDTO<Carrier411ResultDTO<Carrier411QuestionDTO>[]>;

  @Input()
  public workflowSteps!: WorkflowStep[];

  public subCategory!: Carrier411SubCategoryDTO;

  constructor(
    private route: ActivatedRoute,
    protected resultHandler: ResultHandlerService,
    private carrier411AdminService: Carrier411AdminService,
    private carrier411RolesService: Carrier411RolesService
  ) {
    super(resultHandler);
  }

  ngOnInit() {
    this.setContentItems(this.questions);
    this.filters = ContentTypeUtils.getPendingFilters(this.carrier411RolesService.isPublisher());
  }

  loadContentItems() {
    this.carrier411AdminService.getPendingQuestions(this.workflowSteps).subscribe((data) => {
      this.setContentItems(data);
      this.onConfirmData.emit();
    });
  }

  initializeEditDialog(contentType: Carrier411QuestionDTO): void {
    this.carrier411AdminService.getSubCategoryById(contentType.subCategory.id).subscribe((data) => {
      this.subCategory = data.value;
    });

    this.questionEdit.initializeForm(contentType, false)
  }

  handleDenyAction(action: ContentTypeAction, contentType: Carrier411QuestionDTO) {
    this.carrier411AdminService.getQuestionPreviousStep(contentType.id).subscribe((data) => {
      this.handlePreviousStatus(data, action, contentType);
    });
  }

  updateContentItem(data: ContentTypeStatusChangeDTO) {
    const question = data.contentType as Carrier411QuestionDTO;
    const model: Carrier411QuestionUpdateDTO = {
      workflowStep: data.newStatus,
      text: question.text
    };
    this.carrier411AdminService.updateQuestion(data.contentType.id, model).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.onCloseModal(this.statusChangeDialog);
          this.loadContentItems();
        }
      });
    }, (err) => this.resultHandler.handle(err.error));
  }

  deleteContentItem(data: ContentTypeStatusChangeDTO) {
    this.carrier411AdminService.deleteQuestion(data.contentType.id).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.onCloseModal(this.statusChangeDialog);
          this.loadContentItems();
        }
      });
    }, (err) => this.resultHandler.handle(err.error));
  }

  onConfirmToggleData($event: ContentTypeActionTakenDTO) {
    const actionConverted = $event.action;
    const contentType = $event.contentType;

    switch (actionConverted) {
      case +ContentTypeAction.Hide:
      case +ContentTypeAction.Unhide:
        this.toggleVisibility(contentType as Carrier411QuestionDTO);
        break;
      default:
        this.onCloseModal(this.contentTypeToggleDataDialog);
        break;
    }
  }

  toggleVisibility(question: Carrier411QuestionDTO) {
    this.carrier411AdminService.toggleQuestionVisibility(question.id).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.onCloseModal(this.contentTypeToggleDataDialog);
          this.loadContentItems();
        }
      });
    });
  }

  protected loadNavbar(): void {
  }

}
