import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { FormValidators } from '@syncfusion/ej2-angular-inputs';
import { AccountService } from 'src/app/authentication/services/account.service';
import { Carrier411CategoryUpdateDTO } from '../../interfaces/carrier411-category-update.dto';
import { Carrier411CategoryDTO } from 'src/app/carrier411/shared/interfaces/carrier411-category.dto';
import { ResultHandlerService } from 'src/app/core/services/result.handler.service';
import { InputValidatorService } from 'src/app/shared/services/input-validator.service';
import { Carrier411AdminService } from '../../services/carrier411-admin.service';
import { Carrier411AdminMessages } from '../../utilities/carrier411-admin-messages';

@Component({
  selector: 'category-edit',
  templateUrl: './category-edit.component.html',
  styleUrls: ['./category-edit.component.scss']
})
export class CategoryEditComponent implements OnInit {
  @Output('onClose')
  public onCloseTrigger = new EventEmitter<boolean>();

  public form!: FormGroup;
  public userWarningMessage: string = '';

  private category?: Carrier411CategoryDTO;
  private edited: boolean = false;

  constructor(
    public inputValidatorService: InputValidatorService,
    private carrier411AdminService: Carrier411AdminService,
    private accountService: AccountService,
    private resultHandler: ResultHandlerService
  ) { }

  ngOnInit(): void {
    this.initializeForm(undefined, false);
  }

  initializeForm(category?: Carrier411CategoryDTO, edited: boolean = false) {
    this.category = category;
    this.edited = edited;

    this.form = new FormGroup({
      name: new FormControl(category?.name, [FormValidators.required])
    });

    this.setupUserWarning();
  }

  onSave() {
    this.form.markAllAsTouched();
    if (!this.form.valid) {
      return;
    }

    const model = this.form.getRawValue() as Carrier411CategoryUpdateDTO;
    model.workflowStep = this.category!.workflowStep;

    this.updateCategory(model);
  }

  onClose() {
    this.onCloseTrigger.emit(this.edited);
  }

  updateCategory(model: Carrier411CategoryUpdateDTO) {
    this.carrier411AdminService.updateCategory(this.category!.id, model).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.onCloseTrigger.emit(true);
        }
      });
    }, (err) => this.resultHandler.handle(err.error));
  }

  setupUserWarning() {
    if (this.hasDifferentOwner()) {
      this.userWarningMessage = Carrier411AdminMessages.getUserEditorWarningMessage(this.category?.lastModifiedByName);
    } else {
      this.userWarningMessage = '';
    }
  }

  hasDifferentOwner = (): boolean => this.category?.lastModifiedById != this.accountService.getUser()?.id;
}
