import { Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { FormValidators } from '@syncfusion/ej2-angular-inputs';
import { AccountService } from 'src/app/authentication/services/account.service';
import { Carrier411AnswerUpdateDTO } from 'src/app/carrier411/admin/interfaces/carrier411-answer-update.dto';
import { Carrier411AnswerDTO } from 'src/app/carrier411/shared/interfaces/carrier411-answer.dto';
import { Carrier411QuestionDTO } from 'src/app/carrier411/shared/interfaces/carrier411-question.dto';
import { ResultHandlerService } from 'src/app/core/services/result.handler.service';
import { RichTextEditorWithFileComponent } from 'src/app/shared/components/rich-text-editor-with-file/rich-text-editor-with-file.component';
import { InputValidatorService } from 'src/app/shared/services/input-validator.service';
import { getDefaultRichTextFormat, getDefaultRichTextInlineMode, getDefaultRichTextSettings } from 'src/app/shared/utilities/shared';
import { Carrier411AdminService } from '../../services/carrier411-admin.service';
import { Carrier411AdminMessages } from '../../utilities/carrier411-admin-messages';

@Component({
  selector: 'answer-edit',
  templateUrl: './answer-edit.component.html',
  styleUrls: ['./answer-edit.component.scss']
})
export class AnswerEditComponent implements OnInit {
  public inlineMode: object = getDefaultRichTextInlineMode();
  public toolbarSettings: object = getDefaultRichTextSettings();
  public format: object = getDefaultRichTextFormat();

  public form!: FormGroup;
  public userWarningMessage: string = '';

  @Input('question')
  public question!: Carrier411QuestionDTO;
  @Output('onClose')
  public onCloseTrigger = new EventEmitter<boolean>();

  @ViewChild('richText')
  public richText!: RichTextEditorWithFileComponent;

  public answer?: Carrier411AnswerDTO;
  private edited: boolean = false;

  constructor(
    public inputValidatorService: InputValidatorService,
    private carrier411AdminService: Carrier411AdminService,
    private accountService: AccountService,
    private resultHandler: ResultHandlerService
  ) { }

  ngOnInit(): void {
    this.initializeForm();
  }

  initializeForm(answer?: Carrier411AnswerDTO, edited: boolean = false) {
    this.answer = answer;
    this.edited = edited;
    this.form = new FormGroup({
      text: new FormControl(answer?.text, [FormValidators.required])
    });

    this.setupUserWarning();

    if (this.richText) {
      this.richText.initializeRichText(answer!.text);
    }
  }

  onSave() {
    this.form.markAllAsTouched();
    if (!this.form.valid) {
      return;
    }

    const model = this.form.getRawValue() as Carrier411AnswerUpdateDTO;
    model.workflowStep = this.answer!.workflowStep;

    this.updateAnswer(model);
  }

  onClose() {
    this.richText.richTextEditor.focusOut();
    this.onCloseTrigger.emit(this.edited);
  }

  updateAnswer(model: Carrier411AnswerUpdateDTO) {
    this.richText.richTextEditor.focusOut();

    this.carrier411AdminService.updateAnswer(this.answer!.id, model, this.richText.getFiles()).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.onCloseTrigger.emit(true);
        }
      });
    }, (err) => this.resultHandler.handle(err.error));
  }

  setupUserWarning() {
    if (this.hasDifferentOwner()) {
      this.userWarningMessage = Carrier411AdminMessages.getUserEditorWarningMessage(this.answer?.lastModifiedByName);
    } else {
      this.userWarningMessage = '';
    }
  }

  hasDifferentOwner = (): boolean => this.answer?.lastModifiedById != this.accountService.getUser()?.id;
}
