import { WorkflowStep } from './../../../../../shared/constants/workflow-step.enum';
import { Carrier411Service } from './../../../../../front-end/services/carrier411.service';
import { ServiceResultDTO } from './../../../../../../core/interfaces/service-result/service-result.dto';
import { Carrier411CategoryDTO } from 'src/app/carrier411/shared/interfaces/carrier411-category.dto';
import { Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ResultHandlerService } from 'src/app/core/services/result.handler.service';
import { Carrier411RolesService } from 'src/app/carrier411/shared/services/carrier411-roles.service';
import { ContentTypeManagementComponent } from '../../../content-type-management/content-type-management.component';
import { CategoryEditComponent } from '../../../category-edit/category-edit.component';
import { Carrier411AdminService } from 'src/app/carrier411/admin/services/carrier411-admin.service';
import { ContentTypeUtils } from 'src/app/carrier411/admin/utilities/content-type-utils';
import { ContentTypeAction } from 'src/app/carrier411/admin/constants/content-type-action.enum';
import { ContentTypeStatusChangeDTO } from 'src/app/carrier411/admin/interfaces/content-type-status-change.dto';
import { Carrier411CategoryUpdateDTO } from 'src/app/carrier411/admin/interfaces/carrier411-category-update.dto';
import { ContentTypeActionTakenDTO } from 'src/app/carrier411/admin/interfaces/content-type-action-taken.dto';
import { Carrier411ResultDTO } from 'src/app/carrier411/shared/interfaces/carrier411-result.dto';

@Component({
  selector: 'pending-approval-management-categories',
  templateUrl: './pending-approval-management-categories.component.html',
  styleUrls: ['../pending-approval-management.component.scss']
})
export class Carrier411AdminPendingApprovalManagementCategoriesComponent extends ContentTypeManagementComponent<Carrier411CategoryDTO> implements OnInit {
  @Output()
  onConfirmData = new EventEmitter();

  @ViewChild('categoryEdit')
  public categoryEdit!: CategoryEditComponent;

  @Input()
  public categories!: ServiceResultDTO<Carrier411ResultDTO<Carrier411CategoryDTO>[]>;

  @Input()
  public workflowSteps!: WorkflowStep[];

  constructor(
    private route: ActivatedRoute,
    protected resultHandler: ResultHandlerService,
    private carrier411AdminService: Carrier411AdminService,
    private carrier411RolesService: Carrier411RolesService
  ) {
    super(resultHandler);
  }

  ngOnInit() {
    this.setContentItems(this.categories);
    this.filters = ContentTypeUtils.getPendingFilters(this.carrier411RolesService.isPublisher());
  }

  loadContentItems() {
    this.carrier411AdminService.getPendingCategories(this.workflowSteps).subscribe((data) => {
      this.setContentItems(data);
      this.onConfirmData.emit();
    });
  }

  initializeEditDialog(contentType: Carrier411CategoryDTO): void {
    this.categoryEdit.initializeForm(contentType, false)
  }

  handleDenyAction(action: ContentTypeAction, contentType: Carrier411CategoryDTO) {
    this.carrier411AdminService.getCategoryPreviousStep(contentType.id).subscribe((data) => {
      this.handlePreviousStatus(data, action, contentType);
    });
  }

  updateContentItem(data: ContentTypeStatusChangeDTO) {
    const model: Carrier411CategoryUpdateDTO = {
      name: data.contentType.name,
      workflowStep: data.newStatus
    };
    this.carrier411AdminService.updateCategory(data.contentType.id, model).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.onCloseModal(this.statusChangeDialog);
          this.loadContentItems();
        }
      });
    }, (err) => this.resultHandler.handle(err.error));
  }

  deleteContentItem(data: ContentTypeStatusChangeDTO) {
    this.carrier411AdminService.deleteCategory(data.contentType.id).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.onCloseModal(this.statusChangeDialog);
          this.loadContentItems();
        }
      });
    }, (err) => this.resultHandler.handle(err.error));
  }

  onConfirmToggleData($event: ContentTypeActionTakenDTO) {
    const actionConverted = $event.action;
    const contentType = $event.contentType;

    switch (actionConverted) {
      case +ContentTypeAction.Hide:
      case +ContentTypeAction.Unhide:
        this.toggleVisibility(contentType as Carrier411CategoryDTO);
        break;
      case +ContentTypeAction.RestrictAccess:
      case +ContentTypeAction.UnrestrictAccess:
        this.toggleRestrictAccess(contentType as Carrier411CategoryDTO);
        break;
      default:
        this.onCloseModal(this.contentTypeToggleDataDialog);
        break;
    }

    this.onConfirmData.emit();
  }

  toggleVisibility(category: Carrier411CategoryDTO) {
    this.carrier411AdminService.toggleCategoryVisibility(category.id).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.onCloseModal(this.contentTypeToggleDataDialog);
          this.loadContentItems();
        }
      });
    });
  }

  toggleRestrictAccess(category: Carrier411CategoryDTO) {
    this.carrier411AdminService.toggleCategoryRestrictAccess(category.id).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.onCloseModal(this.contentTypeToggleDataDialog);
          this.loadContentItems();
        }
      });
    });
  }

  protected loadNavbar(): void {
  }

}
