import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ServiceResultDTO } from '../../../../core/interfaces/service-result/service-result.dto';
import { ResultHandlerService } from '../../../../core/services/result.handler.service';
import { ApplicationUrls } from '../../../../shared/constants/application-urls';
import { Carrier411CategoryDTO } from '../../../shared/interfaces/carrier411-category.dto';
import { Carrier411ResultDTO } from '../../../shared/interfaces/carrier411-result.dto';
import { Carrier411SubCategoryUpdateDTO } from '../../interfaces/carrier411-subcategory-update.dto';
import { Carrier411SubCategoryDTO } from '../../../shared/interfaces/carrier411-subcategory.dto';
import { ContentTypeAction } from '../../constants/content-type-action.enum';
import { ContentTypeActionTakenDTO } from '../../interfaces/content-type-action-taken.dto';
import { ContentTypeStatusChangeDTO } from '../../interfaces/content-type-status-change.dto';
import { Carrier411AdminService } from '../../services/carrier411-admin.service';
import { SubCategoryCreateComponent } from '../subcategory-create/subcategory-create.component';
import { SubCategoryEditComponent } from '../subcategory-edit/subcategory-edit.component';
import { ContentItemOrderUpdateDTO } from '../../interfaces/content-item-order-update.dto';
import { ContentTypeManagementComponent } from '../content-type-management/content-type-management.component';
import { ContentTypeUtils } from '../../utilities/content-type-utils';
import { Carrier411AdminNavbarService } from '../../services/carrier411-admin-navbar.service';

@Component({
  selector: 'app-subcategory-management',
  templateUrl: './subcategory-management.component.html',
  styleUrls: ['./subcategory-management.component.scss']
})
export class SubCategoryManagementComponent extends ContentTypeManagementComponent<Carrier411SubCategoryDTO> implements OnInit {
  QuestionLink = ApplicationUrls.Carrier411Admin.Carrier411Management.QuestionManagement.path;

  public category!: Carrier411CategoryDTO;

  @ViewChild('subCategoryCreate')
  public subCategoryCreate!: SubCategoryCreateComponent;
  @ViewChild('subCategoryEdit')
  public subCategoryEdit!: SubCategoryEditComponent;

  constructor(
    private route: ActivatedRoute,
    protected resultHandler: ResultHandlerService,
    private carrier411AdminService: Carrier411AdminService,
    private navService: Carrier411AdminNavbarService
  ) {
    super(resultHandler);

    const data = route.snapshot.data["subCategories"] as ServiceResultDTO<Carrier411ResultDTO<Carrier411SubCategoryDTO>[]>;
    this.setContentItems(data);

    const categoryData = route.snapshot.data["category"] as ServiceResultDTO<Carrier411CategoryDTO>;
    this.category = categoryData.value;

    this.loadNavbar();
  }

  ngOnInit(): void {
    this.filters = ContentTypeUtils.getFilters(false);
    this.setupQuerySubscription();
  }

  loadNavbar() {
    if (this.category != null) {
      this.navService.injectSubCategories(this.category.id, this.contentItems);
    }
  }

  setupQuerySubscription() {
    this.route.queryParams.subscribe(queryParams => {
      if (this.category != null && this.category.id != queryParams.categoryId) {
        this.loadCategory(queryParams.categoryId).then((success) => {
          if (success) {
            this.loadContentItems();
          }
        });
      }
    });
  }

  loadCategory(categoryId: number): Promise<boolean> {
    return new Promise((resolve, reject) => {
      this.carrier411AdminService.getCategoryById(categoryId).subscribe((data) => {
        if (data.isSuccess) {
          this.category = data.value;
          resolve(true);
        } else {
          this.resultHandler.handle(data);
          reject(false);
        }
      });
    });
  }

  onAddContentItem() {
    this.addContentDialog.show();
    this.subCategoryCreate.initializeForm();
  }

  onUpdateOrder(subCategories: ContentItemOrderUpdateDTO[]) {
    this.carrier411AdminService.reorderSubCategories(subCategories).subscribe((data) => {
      this.handleUpdateOrder(data);
    });
  }

  initializeEditDialog(contentType: Carrier411SubCategoryDTO): void {
    this.subCategoryEdit.initializeForm(contentType, false)
  }

  handleDenyAction(action: ContentTypeAction, contentType: Carrier411SubCategoryDTO) {
    this.carrier411AdminService.getSubCategoryPreviousStep(contentType.id).subscribe((data) => {
      this.handlePreviousStatus(data, action, contentType);
    });
  }

  loadContentItems() {
    this.carrier411AdminService.getSubCategories(this.category.id).subscribe((data) => {
      this.setContentItems(data);
    });
  }

  updateContentItem(data: ContentTypeStatusChangeDTO) {
    const model: Carrier411SubCategoryUpdateDTO = {
      name: data.contentType.name,
      workflowStep: data.newStatus
    };
    this.carrier411AdminService.updateSubCategory(data.contentType.id, model).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.onCloseModal(this.statusChangeDialog);
          this.loadContentItems();
        }
      });
    }, (err) => this.resultHandler.handle(err.error));
  }

  deleteContentItem(data: ContentTypeStatusChangeDTO) {
    this.carrier411AdminService.deleteSubCategory(data.contentType.id).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.onCloseModal(this.statusChangeDialog);
          this.loadContentItems();
        }
      });
    }, (err) => this.resultHandler.handle(err.error));
  }

  /* Toggle Data */
  onConfirmToggleData($event: ContentTypeActionTakenDTO) {
    const actionConverted = $event.action;
    const contentType = $event.contentType;

    switch (actionConverted) {
      case +ContentTypeAction.Hide:
      case +ContentTypeAction.Unhide:
        this.toggleVisibility(contentType as Carrier411SubCategoryDTO);
        break;
      default:
        this.onCloseModal(this.contentTypeToggleDataDialog);
        break;
    }
  }

  toggleVisibility(subCategory: Carrier411SubCategoryDTO) {
    this.carrier411AdminService.toggleSubCategoryVisibility(subCategory.id).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.onCloseModal(this.contentTypeToggleDataDialog);
          this.loadContentItems();
        }
      });
    });
  }
}
