import { HttpClient, HttpParams } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { BaseAPIService } from '../../../core/api/base.api.service';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';
import { Carrier411AdminApiEndpoints } from '../constants/carrier411-admin-api.constants';
import { Carrier411CategoryDTO } from '../../shared/interfaces/carrier411-category.dto';
import { WorkflowStep } from '../../shared/constants/workflow-step.enum';
import { ContentItemOrderUpdateDTO } from '../interfaces/content-item-order-update.dto';
import { Carrier411SubCategoryDTO } from '../../shared/interfaces/carrier411-subcategory.dto';
import { Carrier411QuestionDTO } from '../../shared/interfaces/carrier411-question.dto';
import { Carrier411AnswerDTO } from '../../shared/interfaces/carrier411-answer.dto';
import { Carrier411CategoryCreateDTO } from '../interfaces/carrier411-category-create.dto';
import { Carrier411CategoryUpdateDTO } from '../interfaces/carrier411-category-update.dto';
import { Carrier411SubCategoryCreateDTO } from '../interfaces/carrier411-subcategory-create.dto';
import { Carrier411SubCategoryUpdateDTO } from '../interfaces/carrier411-subcategory-update.dto';
import { Carrier411QuestionCreateDTO } from '../interfaces/carrier411-question-create.dto';
import { Carrier411QuestionUpdateDTO } from '../interfaces/carrier411-question-update.dto';
import { Carrier411AnswerCreateDTO } from '../interfaces/carrier411-answer-create.dto';
import { Carrier411AnswerUpdateDTO } from '../interfaces/carrier411-answer-update.dto';
import { Carrier411ResultDTO } from '../../shared/interfaces/carrier411-result.dto';
import { Carrier411PendingApprovalCountDTO } from '../interfaces/carrier411-pending-approval-counter.dto';

@Injectable({
  providedIn: 'root'
})
export class Carrier411AdminApiService extends BaseAPIService {

  constructor(
    @Inject("BASE_API_URL_WEB") baseApiUrl_Web: string, http: HttpClient
  ) {
    super(baseApiUrl_Web, http);
  }

  /* Categories */
  public getCategories() {
    return this.http.get<ServiceResultDTO<Carrier411ResultDTO<Carrier411CategoryDTO>[]>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.GetCategories())
    );
  }

  public getPendingApprovalCount(workflowSteps: WorkflowStep[]) {
    const formData = new FormData();
    formData.append('workflowStepsJsonString', JSON.stringify(workflowSteps));

    return this.http.post<ServiceResultDTO<Carrier411PendingApprovalCountDTO>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.GetPendingApprovalCount()), formData
    );
  }

  public getPendingAnswers(workflowSteps: WorkflowStep[]) {
    const formData = new FormData();
    formData.append('workflowStepsJsonString', JSON.stringify(workflowSteps));

    return this.http.post<ServiceResultDTO<Carrier411ResultDTO<Carrier411AnswerDTO>[]>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.GetPendingAnswers()), formData
    );
  }

  public getPendingCategories(workflowSteps: WorkflowStep[]) {
    const formData = new FormData();
    formData.append('workflowStepsJsonString', JSON.stringify(workflowSteps));

    return this.http.post<ServiceResultDTO<Carrier411ResultDTO<Carrier411CategoryDTO>[]>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.GetPendingCategories()), formData
    );
  }

  public getPendingSubCategories(workflowSteps: WorkflowStep[]) {
    const formData = new FormData();
    formData.append('workflowStepsJsonString', JSON.stringify(workflowSteps));

    return this.http.post<ServiceResultDTO<Carrier411ResultDTO<Carrier411SubCategoryDTO>[]>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.GetPendingSubCategories()), formData
    );
  }

  public getPendingQuestions(workflowSteps: WorkflowStep[]) {
    const formData = new FormData();
    formData.append('workflowStepsJsonString', JSON.stringify(workflowSteps));

    return this.http.post<ServiceResultDTO<Carrier411ResultDTO<Carrier411QuestionDTO>[]>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.GetPendingQuestions()), formData
    );
  }

  public getCategoryById(id: number) {
    return this.http.get<ServiceResultDTO<Carrier411CategoryDTO>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.GetCategoryById(id))
    );
  }

  public getCategoryPreviousStep(contentItemId: number) {
    return this.http.get<ServiceResultDTO<WorkflowStep>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.GetCategoryPreviousStep(contentItemId))
    );
  }

  public createCategory(category: Carrier411CategoryCreateDTO) {
    return this.http.post<ServiceResultDTO<Carrier411CategoryDTO>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.CreateCategory()), category
    );
  }

  public updateCategory(id: number, category: Carrier411CategoryUpdateDTO) {
    return this.http.put<ServiceResultDTO<Carrier411CategoryDTO>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.UpdateCategory(id)), category
    );
  }

  public toggleCategoryRestrictAccess(id: number) {
    return this.http.put<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.ToggleCategoryRestrictAccess(id)), null
    );
  }

  public toggleCategoryVisibility(id: number) {
    return this.http.put<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.ToggleCategoryVisibility(id)), null
    );
  }

  public deleteCategory(id: number) {
    return this.http.delete<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.DeleteCategory(id))
    );
  }

  public reorderCategories(categories: ContentItemOrderUpdateDTO[]) {
    return this.http.post<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.ReorderCategories()), categories
    );
  }

  /* SubCategories */
  public getSubCategories(categoryId: number) {
    let params = new HttpParams()
      .set('categoryId', categoryId ?? '');

    return this.http.get<ServiceResultDTO<Carrier411ResultDTO<Carrier411SubCategoryDTO>[]>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.GetSubCategories()), { params }
    );
  }

  public getSubCategoryById(id: number) {
    return this.http.get<ServiceResultDTO<Carrier411SubCategoryDTO>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.GetSubCategoryById(id))
    );
  }

  public getSubCategoryPreviousStep(contentItemId: number) {
    return this.http.get<ServiceResultDTO<WorkflowStep>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.GetSubCategoryPreviousStep(contentItemId))
    );
  }

  public createSubCategory(subCategory: Carrier411SubCategoryCreateDTO) {
    return this.http.post<ServiceResultDTO<Carrier411SubCategoryDTO>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.CreateSubCategory()), subCategory
    );
  }

  public updateSubCategory(id: number, subCategory: Carrier411SubCategoryUpdateDTO) {
    return this.http.put<ServiceResultDTO<Carrier411SubCategoryDTO>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.UpdateSubCategory(id)), subCategory
    );
  }

  public toggleSubCategoryVisibility(id: number) {
    return this.http.put<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.ToggleSubCategoryVisibility(id)), null
    );
  }

  public deleteSubCategory(id: number) {
    return this.http.delete<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.DeleteSubCategory(id))
    );
  }

  public reorderSubCategories(subCategories: ContentItemOrderUpdateDTO[]) {
    return this.http.post<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.ReorderSubCategories()), subCategories
    );
  }

  /* Questions */
  public getQuestions(subCategory: number) {
    let params = new HttpParams()
      .set('subCategoryId', subCategory ?? '');

    return this.http.get<ServiceResultDTO<Carrier411ResultDTO<Carrier411QuestionDTO>[]>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.GetQuestions()), { params }
    );
  }

  public getQuestionById(id: number) {
    return this.http.get<ServiceResultDTO<Carrier411QuestionDTO>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.GetQuestionById(id))
    );
  }

  public getQuestionPreviousStep(contentItemId: number) {
    return this.http.get<ServiceResultDTO<WorkflowStep>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.GetQuestionPreviousStep(contentItemId))
    );
  }

  public createQuestion(question: Carrier411QuestionCreateDTO) {
    return this.http.post<ServiceResultDTO<Carrier411QuestionDTO>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.CreateQuestion()), question
    );
  }

  public updateQuestion(id: number, question: Carrier411QuestionUpdateDTO) {
    return this.http.put<ServiceResultDTO<Carrier411QuestionDTO>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.UpdateQuestion(id)), question
    );
  }

  public toggleQuestionVisibility(id: number) {
    return this.http.put<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.ToggleQuestionVisibility(id)), null
    );
  }

  public deleteQuestion(id: number) {
    return this.http.delete<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.DeleteQuestion(id))
    );
  }

  public reorderQuestions(questions: ContentItemOrderUpdateDTO[]) {
    return this.http.post<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.ReorderQuestions()), questions
    );
  }

  /* Answers */
  public getAnswers(questionId: number) {
    let params = new HttpParams()
      .set('questionId', questionId ?? '');

    return this.http.get<ServiceResultDTO<Carrier411ResultDTO<Carrier411AnswerDTO>[]>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.GetAnswers()), { params }
    );
  }

  public getAnswerPreviousStep(contentItemId: number) {
    return this.http.get<ServiceResultDTO<WorkflowStep>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.GetAnswerPreviousStep(contentItemId))
    );
  }

  public createAnswer(answer: Carrier411AnswerCreateDTO, files?: File[]) {
    const formData = new FormData();
    formData.append('answerJsonString', JSON.stringify(answer));

    if (files != null) {
      for (let i = 0; i < files.length; i++) {
        const file = files[i];
        formData.append('files', file, file.name);
      }
    }

    return this.http.post<ServiceResultDTO<Carrier411AnswerDTO>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.CreateAnswer()), formData
    );
  }

  public updateAnswer(id: number, answer: Carrier411AnswerUpdateDTO, files?: File[]) {
    const formData = new FormData();
    formData.append('answerJsonString', JSON.stringify(answer));

    if (files != null) {
      for (let i = 0; i < files.length; i++) {
        const file = files[i];
        formData.append('files', file, file.name);
      }
    }

    return this.http.put<ServiceResultDTO<Carrier411AnswerDTO>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.UpdateAnswer(id)), formData
    );
  }

  public toggleAnswerVisibility(id: number) {
    return this.http.put<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.ToggleAnswerVisibility(id)), null
    );
  }

  public deleteAnswer(id: number) {
    return this.http.delete<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(Carrier411AdminApiEndpoints.DeleteAnswer(id))
    );
  }
}
