import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MenuEventArgs } from '@syncfusion/ej2-angular-navigations';
import { Carrier411BaseDTO } from 'src/app/carrier411/shared/interfaces/carrier411-base.dto';
import { Carrier411CategoryDTO } from 'src/app/carrier411/shared/interfaces/carrier411-category.dto';
import { InputValidatorService } from 'src/app/shared/services/input-validator.service';
import { Carrier411AdminMessages } from '../../utilities/carrier411-admin-messages';
import { ContentTypeAction } from '../../constants/content-type-action.enum';
import { ContentTypeActionTakenDTO } from '../../interfaces/content-type-action-taken.dto';

@Component({
  selector: 'content-type-toggle-data',
  templateUrl: './content-type-toggle-data.component.html',
  styleUrls: ['./content-type-toggle-data.component.scss']
})
export class ContentTypeToggleDataComponent implements OnInit {
  @Output('onConfirm')
  public onConfirmTrigger = new EventEmitter<ContentTypeActionTakenDTO>();
  @Output('onClose')
  public onCloseTrigger = new EventEmitter();

  public contentType!: Carrier411BaseDTO;

  public userAction!: ContentTypeAction;
  public title: string = '';
  public description: string = '';
  public acknowledge: string = '';

  public form!: FormGroup;

  constructor(
    public inputValidatorService: InputValidatorService
  ) { }

  ngOnInit(): void {
    this.setupForm();
  }

  setupForm() {
    this.form = new FormGroup({
      acknowledge: new FormControl(false, [Validators.requiredTrue])
    });
  }

  initializeForm(
    userAction: ContentTypeAction,
    contentType: Carrier411BaseDTO | Carrier411CategoryDTO
  ) {
    this.userAction = userAction;
    this.contentType = contentType;

    this.setMessages();
    this.setupForm();
  }

  setMessages() {
    switch (this.userAction) {
      case ContentTypeAction.Hide:
        this.title = 'Hide Content';
        this.description = Carrier411AdminMessages.getHideContentDescription();
        this.acknowledge = Carrier411AdminMessages.getHideContentAcknowledge();
        break;
      case ContentTypeAction.Unhide:
        this.title = 'Unhide Content';
        this.description = Carrier411AdminMessages.getUnhideContentDescription();
        this.acknowledge = Carrier411AdminMessages.getUnhideContentAcknowledge();
        break;
      case ContentTypeAction.RestrictAccess:
        this.title = 'Restrict Access';
        this.description = Carrier411AdminMessages.getRestrictContentDescription();
        this.acknowledge = Carrier411AdminMessages.getRestrictContentAcknowledge();
        break;
      case ContentTypeAction.UnrestrictAccess:
        this.title = 'Unrestrict Access';
        this.description = Carrier411AdminMessages.getUnrestrictContentDescription();
        this.acknowledge = Carrier411AdminMessages.getUnrestrictContentAcknowledge();
        break;
      default:
        this.title = '';
        break;
    }
  }

  onConfirm(args?: MenuEventArgs) {
    this.form.markAllAsTouched();
    if (!this.form.valid) {
      return;
    }

    this.onConfirmTrigger.emit({
      action: this.userAction,
      contentType: this.contentType
    });
  }

  onClose() {
    this.onCloseTrigger.emit();
  }
}
