import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { ContentTypeAction } from '../../constants/content-type-action.enum';
import { WorkflowStep } from 'src/app/carrier411/shared/constants/workflow-step.enum';
import { WorkflowStepUtils } from 'src/app/shared/utilities/workflow-step-utils';
import { AccountService } from 'src/app/authentication/services/account.service';
import { Carrier411RolesService } from 'src/app/carrier411/shared/services/carrier411-roles.service';
import { faArrowRight } from '@fortawesome/pro-regular-svg-icons';
import { ContentTypeStatusChangeDTO } from '../../interfaces/content-type-status-change.dto';
import { Carrier411BaseDTO } from 'src/app/carrier411/shared/interfaces/carrier411-base.dto';
import { ItemModel, MenuEventArgs } from '@syncfusion/ej2-angular-navigations';
import { Carrier411AdminMessages } from '../../utilities/carrier411-admin-messages';

@Component({
  selector: 'content-type-status-change',
  templateUrl: './content-type-status-change.component.html',
  styleUrls: ['./content-type-status-change.component.scss']
})
export class ContentTypeStatusChangeComponent implements OnInit {
  WorkflowStepUtils = WorkflowStepUtils;
  faArrowRight = faArrowRight;

  @Output('onConfirm')
  public onConfirmTrigger = new EventEmitter<ContentTypeStatusChangeDTO>();
  @Output('onClose')
  public onCloseTrigger = new EventEmitter();

  public otherActions: ItemModel[] = [];

  public contentType!: Carrier411BaseDTO;
  public nextStep?: WorkflowStep;

  public userAction?: ContentTypeAction;
  public action: string = '';
  public userWarningMessage: string = '';

  constructor(
    private carrier411RolesService: Carrier411RolesService,
    private accountService: AccountService
  ) { }

  ngOnInit(): void {
  }

  initializeForm(
    userAction: ContentTypeAction,
    contentType: Carrier411BaseDTO,
    nextStep: WorkflowStep | null = null
  ) {
    this.userAction = userAction;
    this.contentType = contentType;

    this.setTitle();

    this.otherActions = [];
    if (nextStep == null) {
      this.setupNextStep();
    } else {
      this.nextStep = nextStep;
    }

    this.setupUserWarning();
  }

  setTitle() {
    switch (this.userAction) {
      case ContentTypeAction.Approve:
      case ContentTypeAction.RequestDelete:
      case ContentTypeAction.Delete:
        this.action = 'Approve';
        break;
      case ContentTypeAction.Deny:
        this.action = 'Deny';
        break;
      default:
        this.action = '';
        break;
    }
  }

  setupNextStep() {
    const isReviewer = this.carrier411RolesService.isReviewer();
    const isPublisher = this.carrier411RolesService.isPublisher();

    if (this.userAction == ContentTypeAction.Approve) {
      switch (+WorkflowStep[this.contentType.workflowStep]) {
        case WorkflowStep.Draft:
          this.nextStep = WorkflowStep.PendingApproval;
          if (isReviewer) {
            this.otherActions.push(this.getConfirm());
            this.otherActions.push(this.getMoveToPendingPublish());
          } else if (isPublisher) {
            this.otherActions.push(this.getConfirm());
            this.otherActions.push(this.getMoveToPublished());
          }
          break;
        case WorkflowStep.PendingApproval:
          this.nextStep = WorkflowStep.PendingPublish;
          if (isPublisher) {
            this.otherActions.push(this.getConfirm());
            this.otherActions.push(this.getMoveToPublished());
          }
          break;
        case WorkflowStep.PendingPublish:
          this.nextStep = WorkflowStep.Published;
          break;
        case WorkflowStep.DeleteRequested:
          this.nextStep = WorkflowStep.PendingDelete;
          if (isPublisher) {
            this.otherActions.push(this.getConfirm());
            this.otherActions.push(this.getMoveToDeleted());
          }
          break;
        case WorkflowStep.PendingDelete:
          this.nextStep = WorkflowStep.Deleted;
          break;
        default:
          this.nextStep = undefined;
      }
    } else if (this.userAction == ContentTypeAction.Delete) {
      switch (+WorkflowStep[this.contentType.workflowStep]) {
        case WorkflowStep.Draft:
          this.nextStep = WorkflowStep.Deleted;
          break;
        default:
          this.nextStep = WorkflowStep.DeleteRequested;
          if (isReviewer) {
            this.otherActions.push(this.getConfirm());
            this.otherActions.push(this.getMoveToPendingDelete());
          } else if (isPublisher) {
            this.otherActions.push(this.getConfirm());
            this.otherActions.push(this.getMoveToDeleted());
          }
      }
    }
    else {
      this.nextStep = undefined;
    }
  }

  setupUserWarning() {
    if (this.isDeleteAction() &&
      this.isDraft() &&
      this.hasDifferentOwner()) {
      this.userWarningMessage = Carrier411AdminMessages.getUserEditorWarningMessage(this.contentType.lastModifiedByName);
    }
    else {
      this.userWarningMessage = '';
    }
  }

  onConfirm(args?: MenuEventArgs) {
    if (args != undefined) {
      this.onConfirmTrigger.emit({
        newStatus: WorkflowStep[args.item.id as keyof typeof WorkflowStep],
        contentType: this.contentType
      });
    } else {
      this.onConfirmTrigger.emit({
        newStatus: +this.nextStep!,
        contentType: this.contentType
      });
    }
  }

  onClose() {
    this.onCloseTrigger.emit();
  }

  getConfirm = (): ItemModel => ({ id: this.nextStep!.toString(), text: 'Confirm' });
  getMoveToPendingPublish = (): ItemModel => ({ id: (+WorkflowStep.PendingPublish).toString(), text: 'Move to Pending Publish' });
  getMoveToPublished = (): ItemModel => ({ id: (+WorkflowStep.Published).toString(), text: 'Move to Published' });
  getMoveToPendingDelete = (): ItemModel => ({ id: (+WorkflowStep.PendingDelete).toString(), text: 'Move to Pending Delete' });
  getMoveToDeleted = (): ItemModel => ({ id: (+WorkflowStep.Deleted).toString(), text: 'Move to Deleted' });

  isButtonGreen(): boolean {
    return this.userAction == ContentTypeAction.Approve;
  }

  isButtonRed(): boolean {
    return this.userAction == ContentTypeAction.Deny ||
      this.userAction == ContentTypeAction.Delete ||
      this.userAction == ContentTypeAction.RequestDelete;
  }

  isDeleteAction = (): boolean => this.userAction == ContentTypeAction.RequestDelete || this.userAction == ContentTypeAction.Delete;

  isDraft = (): boolean => WorkflowStepUtils.compareEnumValue(this.contentType.workflowStep, WorkflowStep.Draft);

  hasDifferentOwner = (): boolean => this.contentType.lastModifiedById != this.accountService.getUser()?.id;
}
