import { Component, Input, OnInit } from '@angular/core';
import { faClock, faUser } from '@fortawesome/pro-light-svg-icons';
import { DateDisplayFunctions } from 'src/app/shared/utilities/shared';

@Component({
  selector: 'content-type-last-modified',
  templateUrl: './content-type-last-modified.component.html',
  styleUrls: ['./content-type-last-modified.component.scss']
})
export class ContentTypeLastModifiedComponent implements OnInit {
  faUser = faUser;
  faClock = faClock;

  @Input()
  public name: string = '';
  @Input()
  public dateOnUTC!: Date;

  constructor() { }

  ngOnInit(): void {
  }

  getFormattedDate = (): string => DateDisplayFunctions.createDateTimeLocalDisplayValueFromUTC(this.dateOnUTC);
}
