import { ToolsList } from '../../../../shared/constants/tools-list.enum';
import { Component, OnInit } from '@angular/core';
import { ToolMessagesDTO } from 'src/app/shared/interfaces/tools/tool-messages.dto';
import { ResultHandlerService } from 'src/app/core/services/result.handler.service';
import { ActivatedRoute } from '@angular/router';
import { ServiceResultDTO } from 'src/app/core/interfaces/service-result/service-result.dto';

@Component({
  selector: 'landing-page-content',
  templateUrl: './landing-page-content.component.html'
})
export class Carrier411AdminLandingPageContentComponent implements OnInit {
  public toolId: number = ToolsList.Carrier411;
  public toolMessages!: ToolMessagesDTO;

  constructor(
    route: ActivatedRoute,
    private resultHandlerService: ResultHandlerService
  ) {
    const data = route.snapshot.data["messages"] as ServiceResultDTO<ToolMessagesDTO>;
    if (data.isSuccess) {
      this.toolMessages = data.value;
    } else {
      this.resultHandlerService.handle(data);
    }
  }

  ngOnInit(): void {
  }
}
