import { Injectable } from "@angular/core";
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from "@angular/router";
import { Observable } from 'rxjs';
import { ServiceResultDTO } from "src/app/core/interfaces/service-result/service-result.dto";
import { Carrier411QuestionDTO } from "../../shared/interfaces/carrier411-question.dto";
import { Carrier411ResultDTO } from "../../shared/interfaces/carrier411-result.dto";
import { Carrier411AdminService } from '../services/carrier411-admin.service';

@Injectable()
export class QuestionsListResolver implements Resolve<ServiceResultDTO<Carrier411ResultDTO<Carrier411QuestionDTO>[]>> {

  constructor(private carrier411AdminService: Carrier411AdminService) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<ServiceResultDTO<Carrier411ResultDTO<Carrier411QuestionDTO>[]>> {
    const subCategoryId = Number(route.queryParamMap.get('subCategoryId'));
    return this.carrier411AdminService.getQuestions(subCategoryId);
  }
}
