import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { FormValidators } from '@syncfusion/ej2-angular-inputs';
import { WorkflowStep } from 'src/app/carrier411/shared/constants/workflow-step.enum';
import { Carrier411CategoryCreateDTO } from '../../interfaces/carrier411-category-create.dto';
import { ResultHandlerService } from 'src/app/core/services/result.handler.service';
import { InputValidatorService } from 'src/app/shared/services/input-validator.service';
import { SaveContentType } from '../../constants/save-content-type.enum';
import { Carrier411AdminService } from '../../services/carrier411-admin.service';
import { ContentTypeUtils } from '../../utilities/content-type-utils';

@Component({
  selector: 'subcategory-create',
  templateUrl: './subcategory-create.component.html'
})
export class SubCategoryCreateComponent implements OnInit {

  WorkflowStep = WorkflowStep;

  public form!: FormGroup;

  @Input()
  public categoryId?: number;

  @Input()
  public categoryName?: string;

  @Output('onClose')
  public onCloseTrigger = new EventEmitter<boolean>();

  private created: boolean = false;

  constructor(
    public inputValidatorService: InputValidatorService,
    private carrier411AdminService: Carrier411AdminService,
    private resultHandler: ResultHandlerService
  ) { }

  ngOnInit(): void {
    this.initializeForm();
  }

  initializeForm(created: boolean = false) {
    this.form = new FormGroup({
      name: new FormControl('', [FormValidators.required])
    });

    this.created = created;
  }

  onSave(option: SaveContentType) {
    this.form.markAllAsTouched();
    if (!this.form.valid) {
      return;
    }

    const model = this.form.getRawValue() as Carrier411CategoryCreateDTO;
    model.workflowStep = ContentTypeUtils.getWorkflowStep(option);

    this.saveSubCategory(model, option);
  }

  onClose() {
    this.onCloseTrigger.emit(this.created);
  }

  saveSubCategory(model: Carrier411CategoryCreateDTO, step: SaveContentType) {
    model.categoryId = this.categoryId!;
    this.carrier411AdminService.createSubCategory(model).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          if (ContentTypeUtils.finishSaveAction(step)) {
            this.onCloseTrigger.emit(true);
          } else {
            this.initializeForm(true);
          }
        }
      });
    }, (err) => this.resultHandler.handle(err.error));
  }
}
