import { Component, Input, OnChanges, ViewEncapsulation } from '@angular/core';
import { UrlUtils } from 'src/app/shared/utilities/url-utils';

@Component({
  selector: 'answer-text',
  templateUrl: './answer-text.component.html',
  styleUrls: ['./answer-text.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class AnswerTextComponent implements OnChanges {
  @Input('text')
  public text: string | null = null;

  constructor() { }

  ngOnChanges() {
    this.text = this.getAnswerText();
  }

  private getAnswerText(): string | null {
    if (this.text == null) return this.text;

    const container = document.createElement('div');
    container.innerHTML = this.text;

    const elementsWithFiles = container.querySelectorAll('*[file]:not([file=""])');
    elementsWithFiles.forEach(e => {
      const link = document.createElement('a');
      link.href = e.getAttribute('file')!;
      link.innerHTML = e.innerHTML;
      link.target = '_blank';
      link.download = '';
      link.title = UrlUtils.getPrettyFileName(link.href);

      e.removeAttribute('file');
      e.innerHTML = link.outerHTML;
    });

    return container.innerHTML;    
  }
}
