import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';
import { WorkflowStep } from '../../shared/constants/workflow-step.enum';
import { Carrier411AnswerDTO } from '../../shared/interfaces/carrier411-answer.dto';
import { Carrier411CategoryDTO } from '../../shared/interfaces/carrier411-category.dto';
import { Carrier411SubCategoryDTO } from '../../shared/interfaces/carrier411-subcategory.dto';
import { ContentItemOrderUpdateDTO } from '../interfaces/content-item-order-update.dto';
import { Carrier411QuestionDTO } from '../../shared/interfaces/carrier411-question.dto';
import { Carrier411AdminApiService } from './carrier411-admin.api.service';
import { Carrier411ResultDTO } from '../../shared/interfaces/carrier411-result.dto';
import { Carrier411AnswerCreateDTO } from '../interfaces/carrier411-answer-create.dto';
import { Carrier411AnswerUpdateDTO } from '../interfaces/carrier411-answer-update.dto';
import { Carrier411QuestionUpdateDTO } from '../interfaces/carrier411-question-update.dto';
import { Carrier411QuestionCreateDTO } from '../interfaces/carrier411-question-create.dto';
import { Carrier411SubCategoryUpdateDTO } from '../interfaces/carrier411-subcategory-update.dto';
import { Carrier411SubCategoryCreateDTO } from '../interfaces/carrier411-subcategory-create.dto';
import { Carrier411CategoryCreateDTO } from '../interfaces/carrier411-category-create.dto';
import { Carrier411CategoryUpdateDTO } from '../interfaces/carrier411-category-update.dto';
import { Carrier411PendingApprovalCountDTO } from '../interfaces/carrier411-pending-approval-counter.dto';

@Injectable({
  providedIn: 'root'
})
export class Carrier411AdminService {
  constructor(private carrier411AdminApiService: Carrier411AdminApiService) { }

  /* Categories */
  public getCategories(): Observable<ServiceResultDTO<Carrier411ResultDTO<Carrier411CategoryDTO>[]>> {
    return this.carrier411AdminApiService.getCategories();
  }

  public getPendingApprovalCount(workflowSteps: WorkflowStep[]): Observable<ServiceResultDTO<Carrier411PendingApprovalCountDTO>> {
    return this.carrier411AdminApiService.getPendingApprovalCount(workflowSteps);
  }

  public getPendingAnswers(workflowSteps: WorkflowStep[]): Observable<ServiceResultDTO<Carrier411ResultDTO<Carrier411AnswerDTO>[]>> {
    return this.carrier411AdminApiService.getPendingAnswers(workflowSteps);
  }

  public getPendingCategories(workflowSteps: WorkflowStep[]): Observable<ServiceResultDTO<Carrier411ResultDTO<Carrier411CategoryDTO>[]>> {
    return this.carrier411AdminApiService.getPendingCategories(workflowSteps);
  }

  public getPendingSubCategories(workflowSteps: WorkflowStep[]): Observable<ServiceResultDTO<Carrier411ResultDTO<Carrier411SubCategoryDTO>[]>> {
    return this.carrier411AdminApiService.getPendingSubCategories(workflowSteps);
  }

  public getPendingQuestions(workflowSteps: WorkflowStep[]): Observable<ServiceResultDTO<Carrier411ResultDTO<Carrier411QuestionDTO>[]>> {
    return this.carrier411AdminApiService.getPendingQuestions(workflowSteps);
  }

  public getCategoryById(id: number): Observable<ServiceResultDTO<Carrier411CategoryDTO>> {
    return this.carrier411AdminApiService.getCategoryById(id);
  }

  public getCategoryPreviousStep(contentItemId: number): Observable<ServiceResultDTO<WorkflowStep>> {
    return this.carrier411AdminApiService.getCategoryPreviousStep(contentItemId);
  }

  public createCategory(category: Carrier411CategoryCreateDTO): Observable<ServiceResultDTO<Carrier411CategoryDTO>> {
    return this.carrier411AdminApiService.createCategory(category);
  }

  public updateCategory(id: number, category: Carrier411CategoryUpdateDTO): Observable<ServiceResultDTO<Carrier411CategoryDTO>> {
    return this.carrier411AdminApiService.updateCategory(id, category);
  }

  public toggleCategoryRestrictAccess(id: number): Observable<ServiceResultDTO<boolean>> {
    return this.carrier411AdminApiService.toggleCategoryRestrictAccess(id);
  }

  public toggleCategoryVisibility(id: number): Observable<ServiceResultDTO<boolean>> {
    return this.carrier411AdminApiService.toggleCategoryVisibility(id);
  }

  public deleteCategory(id: number): Observable<ServiceResultDTO<boolean>> {
    return this.carrier411AdminApiService.deleteCategory(id);
  }

  public reorderCategories(categories: ContentItemOrderUpdateDTO[]): Observable<ServiceResultDTO<boolean>> {
    return this.carrier411AdminApiService.reorderCategories(categories);
  }

  /* SubCategories */
  public getSubCategories(categoryId: number): Observable<ServiceResultDTO<Carrier411ResultDTO<Carrier411SubCategoryDTO>[]>> {
    return this.carrier411AdminApiService.getSubCategories(categoryId);
  }

  public getSubCategoryById(id: number): Observable<ServiceResultDTO<Carrier411SubCategoryDTO>> {
    return this.carrier411AdminApiService.getSubCategoryById(id);
  }

  public getSubCategoryPreviousStep(contentItemId: number): Observable<ServiceResultDTO<WorkflowStep>> {
    return this.carrier411AdminApiService.getSubCategoryPreviousStep(contentItemId);
  }

  public createSubCategory(subCategory: Carrier411SubCategoryCreateDTO): Observable<ServiceResultDTO<Carrier411SubCategoryDTO>> {
    return this.carrier411AdminApiService.createSubCategory(subCategory);
  }

  public updateSubCategory(id: number, subCategory: Carrier411SubCategoryUpdateDTO) {
    return this.carrier411AdminApiService.updateSubCategory(id, subCategory);
  }

  public toggleSubCategoryVisibility(id: number) {
    return this.carrier411AdminApiService.toggleSubCategoryVisibility(id);
  }

  public deleteSubCategory(id: number) {
    return this.carrier411AdminApiService.deleteSubCategory(id);
  }

  public reorderSubCategories(subCategories: ContentItemOrderUpdateDTO[]) {
    return this.carrier411AdminApiService.reorderSubCategories(subCategories);
  }

  /* Questions */
  public getQuestions(subCategoryId: number): Observable<ServiceResultDTO<Carrier411ResultDTO<Carrier411QuestionDTO>[]>> {
    return this.carrier411AdminApiService.getQuestions(subCategoryId);
  }

  public getQuestionById(id: number): Observable<ServiceResultDTO<Carrier411QuestionDTO>> {
    return this.carrier411AdminApiService.getQuestionById(id);
  }

  public getQuestionPreviousStep(contentItemId: number): Observable<ServiceResultDTO<WorkflowStep>> {
    return this.carrier411AdminApiService.getQuestionPreviousStep(contentItemId);
  }

  public createQuestion(question: Carrier411QuestionCreateDTO): Observable<ServiceResultDTO<Carrier411QuestionDTO>> {
    return this.carrier411AdminApiService.createQuestion(question);
  }

  public updateQuestion(id: number, question: Carrier411QuestionUpdateDTO) {
    return this.carrier411AdminApiService.updateQuestion(id, question);
  }

  public toggleQuestionVisibility(id: number) {
    return this.carrier411AdminApiService.toggleQuestionVisibility(id);
  }

  public deleteQuestion(id: number) {
    return this.carrier411AdminApiService.deleteQuestion(id);
  }

  public reorderQuestions(questions: ContentItemOrderUpdateDTO[]) {
    return this.carrier411AdminApiService.reorderQuestions(questions);
  }

  /* Answers */
  public getAnswers(questionId: number): Observable<ServiceResultDTO<Carrier411ResultDTO<Carrier411AnswerDTO>[]>> {
    return this.carrier411AdminApiService.getAnswers(questionId);
  }

  public getAnswerPreviousStep(contentItemId: number): Observable<ServiceResultDTO<WorkflowStep>> {
    return this.carrier411AdminApiService.getAnswerPreviousStep(contentItemId);
  }

  public createAnswer(answer: Carrier411AnswerCreateDTO, files: File[]): Observable<ServiceResultDTO<Carrier411AnswerDTO>> {
    return this.carrier411AdminApiService.createAnswer(answer, files);
  }

  public updateAnswer(id: number, answer: Carrier411AnswerUpdateDTO, files?: File[]) {
    return this.carrier411AdminApiService.updateAnswer(id, answer, files);
  }

  public toggleAnswerVisibility(id: number) {
    return this.carrier411AdminApiService.toggleAnswerVisibility(id);
  }

  public deleteAnswer(id: number) {
    return this.carrier411AdminApiService.deleteAnswer(id);
  }
}
