import { Component, ViewChild } from '@angular/core';
import { DialogComponent } from '@syncfusion/ej2-angular-popups';
import { WorkflowStep } from 'src/app/carrier411/shared/constants/workflow-step.enum';
import { Carrier411BaseDTO } from 'src/app/carrier411/shared/interfaces/carrier411-base.dto';
import { Carrier411ResultDTO } from 'src/app/carrier411/shared/interfaces/carrier411-result.dto';
import { ServiceResultDTO } from 'src/app/core/interfaces/service-result/service-result.dto';
import { ResultHandlerService } from 'src/app/core/services/result.handler.service';
import { CommonDTO } from 'src/app/shared/interfaces/common.dto';
import { WorkflowStepUtils } from 'src/app/shared/utilities/workflow-step-utils';
import { Carrier411FilterEnum } from '../../constants/carrier411-filter.enum';
import { ContentTypeAction } from '../../constants/content-type-action.enum';
import { ContentTypeActionTakenDTO } from '../../interfaces/content-type-action-taken.dto';
import { ContentTypeStatusChangeDTO } from '../../interfaces/content-type-status-change.dto';
import { ContentTypeManageOrderComponent } from '../content-type-manage-order/content-type-manage-order.component';
import { ContentTypeStatusChangeComponent } from '../content-type-status-change/content-type-status-change.component';
import { ContentTypeToggleDataComponent } from '../content-type-toggle-data/content-type-toggle-data.component';

@Component({ template: '' })
export abstract class ContentTypeManagementComponent<TDTO extends Carrier411BaseDTO> {
  public contentItems!: Carrier411ResultDTO<TDTO>[];
  public contentItemsList!: Carrier411ResultDTO<TDTO>[];
  public filters!: CommonDTO[];
  public fields: Object = { text: 'name', value: 'id' };

  @ViewChild('ejAddContentDialog')
  public addContentDialog!: DialogComponent;
  @ViewChild('ejEditContentDialog')
  public editContentDialog!: DialogComponent;

  @ViewChild('ejManageOrderDialog')
  public manageOrderDialog!: DialogComponent;
  @ViewChild('contentTypeManageOrder')
  public contentTypeManageOrder!: ContentTypeManageOrderComponent;

  @ViewChild('ejStatusChangeDialog')
  public statusChangeDialog!: DialogComponent;
  @ViewChild('contentTypeStatusChange')
  public contentTypeStatusChange!: ContentTypeStatusChangeComponent;

  @ViewChild('ejToggleDataDialog')
  public contentTypeToggleDataDialog!: DialogComponent;
  @ViewChild('contentTypeToggleData')
  public contentTypeToggleData!: ContentTypeToggleDataComponent;

  public currentFilterId: number | undefined = 0;

  constructor(
    protected resultHandler: ResultHandlerService
  ) {
  }

  public onOpenModal(args: any): void {
    //Preventing the default dialog focus
    args.preventFocus = true;
  }

  public onCloseModal(modal: DialogComponent): void {
    modal.hide();
  }

  public onFilterChanged(event: any) {
    const filter = event.itemData;
    this.currentFilterId = filter.id;

    this.contentItemsList = this.getFilteredContentItems();
  }

  public getFilteredContentItems(): Carrier411ResultDTO<TDTO>[] {
    const filtered = this.contentItems.filter(c => c.item.nonPublishedItemId == undefined);
    const hasFilter = this.currentFilterId != undefined && this.currentFilterId != null && this.currentFilterId != 0;
    if (!hasFilter) {
      return filtered;
    } else if (this.currentFilterId == +Carrier411FilterEnum.Hidden) {
      return filtered.filter(c => c.item.isHidden);
    } else if (this.currentFilterId == +Carrier411FilterEnum.Restricted) {
      return filtered.filter(c => (c.item as any)?.isRestricted);
    }

    return filtered.filter(c => +Carrier411FilterEnum[c.item.workflowStep] == this.currentFilterId)
  }

  public onOverlayClick(reload: boolean = false): void {
    this.addContentDialog?.hide();
    this.editContentDialog?.hide();
    if (reload) {
      this.loadContentItems();
    }
  }

  public onManageOrder(): void {
    const contentToManage = this.contentItems.filter(c => c.item.nonPublishedItemId == undefined).map(c => c.item);
    this.contentTypeManageOrder.initializeForm(contentToManage);
    this.manageOrderDialog.show();
  }

  public onConfirmStatusChange($event: ContentTypeStatusChangeDTO) {
    if (WorkflowStepUtils.compareEnumValue($event.newStatus, WorkflowStep.Deleted)) {
      this.deleteContentItem($event);
    }
    else {
      this.updateContentItem($event);
    }
  }

  public onTakeAction($event: ContentTypeActionTakenDTO) {
    const actionConverted = +ContentTypeAction[$event.action];
    const contentType = $event.contentType;

    switch (actionConverted) {
      case ContentTypeAction.Approve:
      case ContentTypeAction.RequestDelete:
      case ContentTypeAction.Delete:
        this.statusChangeDialog.show();
        this.contentTypeStatusChange.initializeForm(actionConverted, contentType);
        break;
      case ContentTypeAction.Deny:
        this.handleDenyAction(actionConverted, contentType as TDTO);
        break;
      case ContentTypeAction.Edit:
        this.editContentDialog.show();
        this.initializeEditDialog(contentType as TDTO);
        break;
      case ContentTypeAction.Hide:
      case ContentTypeAction.Unhide:
      case ContentTypeAction.RestrictAccess:
      case ContentTypeAction.UnrestrictAccess:
        this.contentTypeToggleData.initializeForm(actionConverted, contentType as TDTO);
        this.contentTypeToggleDataDialog.show();
        break;
      default:
        break;
    }
  }

  protected abstract loadContentItems(): void;
  protected abstract loadNavbar(): void;

  protected abstract initializeEditDialog(contentType: Carrier411BaseDTO): void;
  protected abstract handleDenyAction(action: ContentTypeAction, contentType: TDTO): void;

  protected abstract updateContentItem($event: ContentTypeStatusChangeDTO): void;
  protected abstract deleteContentItem($event: ContentTypeStatusChangeDTO): void;

  protected handlePreviousStatus(data: ServiceResultDTO<WorkflowStep>, action: ContentTypeAction, contentType: TDTO) {
    if (data.isSuccess) {
      this.contentTypeStatusChange.initializeForm(action, contentType, +WorkflowStep[data.value]);
      this.statusChangeDialog.show();
    } else {
      this.resultHandler.handle(data);
    }
  }

  protected handleUpdateOrder(data: ServiceResultDTO<boolean>) {
    this.resultHandler.handle(data).then((success) => {
      if (success) {
        this.loadContentItems();
        this.manageOrderDialog.hide();
      }
    })
  }

  protected setContentItems(data: ServiceResultDTO<Carrier411ResultDTO<TDTO>[]>) {
    if (data.isSuccess) {
      this.contentItems = data.value;
      this.contentItemsList = this.getFilteredContentItems();
      this.loadNavbar();
    } else {
      this.resultHandler.handle(data);
    }
  }
}
