import { CommonDTO } from "src/app/shared/interfaces/common.dto";
import { WorkflowStep } from "../../shared/constants/workflow-step.enum";
import { Carrier411FilterEnum } from "../constants/carrier411-filter.enum";
import { SaveContentType } from "../constants/save-content-type.enum";

export abstract class ContentTypeUtils {
  public static getWorkflowStep(option: SaveContentType): WorkflowStep {
    switch (+SaveContentType[option]) {
      case SaveContentType.Save:
      case SaveContentType.SaveAddMore:
        return WorkflowStep.Draft;
      case SaveContentType.PendingApproval:
      case SaveContentType.PendingApprovalAddMore:
        return WorkflowStep.PendingApproval;
      case SaveContentType.PendingPublish:
      case SaveContentType.PendingPublishAddMore:
        return WorkflowStep.PendingPublish;
      case SaveContentType.Publish:
      case SaveContentType.PublishAddMore:
        return WorkflowStep.Published;
      default:
        return WorkflowStep.Draft;
    }
  }

  public static finishSaveAction(option: SaveContentType): boolean {
    const id = +SaveContentType[option];
    return id == SaveContentType.Save ||
      id == SaveContentType.PendingApproval ||
      id == +SaveContentType.PendingPublish ||
      id == +SaveContentType.Publish;
  }

  public static getFilters(showRestricted: boolean): CommonDTO[] {
    const filters = [
      { id: +Carrier411FilterEnum.All, name: 'All' },
      { id: +Carrier411FilterEnum.Draft, name: 'Draft' },
      { id: +Carrier411FilterEnum.PendingApproval, name: 'Pending Approval' },
      { id: +Carrier411FilterEnum.PendingPublish, name: 'Pending Publish' },
      { id: +Carrier411FilterEnum.Published, name: 'Published' },
      { id: +Carrier411FilterEnum.DeleteRequested, name: 'Delete Requested' },
      { id: +Carrier411FilterEnum.PendingDelete, name: 'Pending Delete' },
      { id: +Carrier411FilterEnum.Hidden, name: 'Hidden' }
    ];

    if (showRestricted) {
      filters.push({
        id: +Carrier411FilterEnum.Restricted,
        name: 'Restricted'
      });
    }

    return filters;
  }

  public static getPendingFilters(isPublisher: boolean): CommonDTO[] {
    const filters = [
      { id: +Carrier411FilterEnum.All, name: 'All' },
      { id: +Carrier411FilterEnum.DeleteRequested, name: 'Delete Requested' },
      { id: +Carrier411FilterEnum.PendingApproval, name: 'Pending Approval' }
    ];

    if (isPublisher) {
      filters.push({ id: +Carrier411FilterEnum.PendingDelete, name: 'Pending Delete' });
      filters.push({ id: +Carrier411FilterEnum.PendingPublish, name: 'Pending Publish' });
    }

    return filters;
  }
}
