import { Injectable } from '@angular/core';
import { Resolve, RouterStateSnapshot, ActivatedRouteSnapshot } from '@angular/router';
import { Observable } from 'rxjs';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';
import { Carrier411AnswerDTO } from '../../shared/interfaces/carrier411-answer.dto';
import { Carrier411ResultDTO } from '../../shared/interfaces/carrier411-result.dto';
import { Carrier411AdminService } from '../services/carrier411-admin.service';

@Injectable()
export class AnswersListResolver implements Resolve<ServiceResultDTO<Carrier411ResultDTO<Carrier411AnswerDTO>[]>> {

  constructor(private carrier411AdminService: Carrier411AdminService) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<ServiceResultDTO<Carrier411ResultDTO<Carrier411AnswerDTO>[]>> {
    const questionId = Number(route.queryParamMap.get('questionId'));
    return this.carrier411AdminService.getAnswers(questionId);
  }
}
