import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { FormValidators } from '@syncfusion/ej2-angular-inputs';
import { AccountService } from 'src/app/authentication/services/account.service';
import { Carrier411QuestionUpdateDTO } from '../../interfaces/carrier411-question-update.dto';
import { Carrier411QuestionDTO } from 'src/app/carrier411/shared/interfaces/carrier411-question.dto';
import { ResultHandlerService } from 'src/app/core/services/result.handler.service';
import { InputValidatorService } from 'src/app/shared/services/input-validator.service';
import { Carrier411AdminService } from '../../services/carrier411-admin.service';
import { Carrier411AdminMessages } from '../../utilities/carrier411-admin-messages';

@Component({
  selector: 'question-edit',
  templateUrl: './question-edit.component.html',
  styleUrls: ['./question-edit.component.scss']
})
export class QuestionEditComponent implements OnInit {
  @Input()
  public categoryName!: string | undefined;
  @Input()
  public subcategoryName!: string | undefined;
  @Input()
  public subCategoryId!: number | undefined;

  @Output('onClose')
  public onCloseTrigger = new EventEmitter<boolean>();

  public form!: FormGroup;
  public userWarningMessage: string = '';

  private question?: Carrier411QuestionDTO;
  private edited: boolean = false;

  constructor(
    public inputValidatorService: InputValidatorService,
    private carrier411AdminService: Carrier411AdminService,
    private accountService: AccountService,
    private resultHandler: ResultHandlerService
  ) { }

  ngOnInit(): void {
    this.initializeForm(undefined, false);
  }

  initializeForm(question?: Carrier411QuestionDTO, edited: boolean = false) {
    this.question = question;
    this.edited = edited;

    this.form = new FormGroup({
      text: new FormControl(question?.text, [FormValidators.required])
    });

    this.setupUserWarning();
  }

  onSave() {
    this.form.markAllAsTouched();
    if (!this.form.valid) {
      return;
    }

    const model = this.form.getRawValue() as Carrier411QuestionUpdateDTO;
    model.workflowStep = this.question!.workflowStep;
    this.updateQuestion(model);
  }

  onClose() {
    this.onCloseTrigger.emit(this.edited);
  }

  updateQuestion(model: Carrier411QuestionUpdateDTO) {
    this.carrier411AdminService.updateQuestion(this.question!.id, model).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.onCloseTrigger.emit(true);
        }
      });
    }, (err) => this.resultHandler.handle(err.error));
  }

  setupUserWarning() {
    if (this.hasDifferentOwner()) {
      this.userWarningMessage = Carrier411AdminMessages.getUserEditorWarningMessage(this.question?.lastModifiedByName);
    }
    else {
      this.userWarningMessage = '';
    }
  }

  hasDifferentOwner = (): boolean => this.question?.lastModifiedById != this.accountService.getUser()?.id;

}
