import { Injectable } from "@angular/core";
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from "@angular/router";
import { Observable } from "rxjs";
import { ServiceResultDTO } from "src/app/core/interfaces/service-result/service-result.dto";
import { WorkflowStep } from "../../shared/constants/workflow-step.enum";
import { Carrier411CategoryDTO } from "../../shared/interfaces/carrier411-category.dto";
import { Carrier411ResultDTO } from "../../shared/interfaces/carrier411-result.dto";
import { Carrier411RolesService } from "../../shared/services/carrier411-roles.service";
import { Carrier411AdminService } from "../services/carrier411-admin.service";

@Injectable()
export class PendingCategoriesListResolver implements Resolve<ServiceResultDTO<Carrier411ResultDTO<Carrier411CategoryDTO>[]>> {

  constructor(
    private carrier411AdminService: Carrier411AdminService,
    private carrier411RolesService: Carrier411RolesService) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<ServiceResultDTO<Carrier411ResultDTO<Carrier411CategoryDTO>[]>> {
    var workFlowSteps = this.carrier411RolesService.getAllowedWorkflowSteps();
    return this.carrier411AdminService.getPendingCategories(workFlowSteps);
  }
}
