import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CarrierDTO } from 'src/app/admin/carrier-management/interfaces/carrier.dto';
import { ToolService } from 'src/app/admin/tools-management/services/tool.service';
import { Carrier411AnswerUpdateDTO } from 'src/app/carrier411/admin/interfaces/carrier411-answer-update.dto';
import { Carrier411AnswerDTO } from 'src/app/carrier411/shared/interfaces/carrier411-answer.dto';
import { Carrier411QuestionDTO } from 'src/app/carrier411/shared/interfaces/carrier411-question.dto';
import { Carrier411ResultDTO } from 'src/app/carrier411/shared/interfaces/carrier411-result.dto';
import { ServiceResultDTO } from 'src/app/core/interfaces/service-result/service-result.dto';
import { ResultHandlerService } from 'src/app/core/services/result.handler.service';
import { ToolsList } from 'src/app/shared/constants/tools-list.enum';
import { ContentTypeAction } from '../../constants/content-type-action.enum';
import { ContentTypeActionTakenDTO } from '../../interfaces/content-type-action-taken.dto';
import { ContentTypeStatusChangeDTO } from '../../interfaces/content-type-status-change.dto';
import { Carrier411AdminNavbarService } from '../../services/carrier411-admin-navbar.service';
import { Carrier411AdminService } from '../../services/carrier411-admin.service';
import { ContentTypeUtils } from '../../utilities/content-type-utils';
import { AnswerCreateComponent } from '../answer-create/answer-create.component';
import { AnswerEditComponent } from '../answer-edit/answer-edit.component';
import { ContentTypeManagementComponent } from '../content-type-management/content-type-management.component';

@Component({
  selector: 'app-answer-management',
  templateUrl: './answer-management.component.html',
  styleUrls: ['./answer-management.component.scss']
})
export class AnswerManagementComponent extends ContentTypeManagementComponent<Carrier411AnswerDTO> implements OnInit {
  public question!: Carrier411QuestionDTO;
  private carriers!: CarrierDTO[];

  @ViewChild('answerCreate')
  public answerCreate!: AnswerCreateComponent;
  @ViewChild('answerEdit')
  public answerEdit!: AnswerEditComponent;

  public carriersAvailable: boolean = true;
  public showHeader: boolean = true;

  constructor(
    private route: ActivatedRoute,
    protected resultHandler: ResultHandlerService,
    private carrier411AdminService: Carrier411AdminService,
    private toolService: ToolService,
    private navService: Carrier411AdminNavbarService
  ) {
    super(resultHandler);

    const data = route.snapshot.data["answers"] as ServiceResultDTO<Carrier411ResultDTO<Carrier411AnswerDTO>[]>;
    this.setContentItems(data);

    const questionData = route.snapshot.data["question"] as ServiceResultDTO<Carrier411QuestionDTO>;
    this.question = questionData.value;

    this.loadNavbar();
  }

  ngOnInit(): void {
    this.filters = ContentTypeUtils.getFilters(false);

    this.loadCarriers();

    this.setupQuerySubscription();
  }

  loadNavbar(): void {
    if (this.question != null) {
      this.navService.injectAnswers(this.question.id, this.contentItems, this.route.snapshot.queryParams.answerId);
    }
  }

  showFilter(): boolean {
    return this.route.snapshot.params.answerId == null;
  }

  setupQuerySubscription(): void {
    this.route.queryParams.subscribe(queryParams => {
      if (this.question != null && this.question.id != queryParams.questionId) {
        this.loadQuestion(queryParams.questionId).then((success) => {
          if (success) {
            this.loadContentItems().then((success) => {
              if (success) {
                this.filterAnswers(queryParams.answerId);
              }
            });
          }
        });
      } else {
        this.filterAnswers(queryParams.answerId);
      }
    });
  }

  filterAnswers(answerId?: number): void {
    if (answerId != null) {
      const answer = this.contentItems.filter(a => a.item.id == answerId);
      this.contentItemsList = answer;
      this.showHeader = false;
    } else {
      this.contentItemsList = this.getFilteredContentItems();
      this.showHeader = true;
    }
  }

  loadQuestion(questionId: number): Promise<boolean> {
    return new Promise((resolve, reject) => {
      this.carrier411AdminService.getQuestionById(questionId).subscribe((data) => {
        if (data.isSuccess) {
          this.question = data.value;
          resolve(true);
        } else {
          this.resultHandler.handle(data);
          reject(false);
        }
      });
    });
  }

  loadCarriers(): void {
    this.toolService.getToolActiveCarriers(ToolsList.Carrier411).subscribe((data) => {
      if (data.isSuccess) {
        this.carriers = data.value;
        this.carriersAvailable = this.getAvailableCarriers()?.length > 0;
      } else {
        this.resultHandler.handle(data);
      }
    });
  }

  getAvailableCarriers(): CarrierDTO[] {
    const carrierAnswers = this.contentItems?.map(a => a.item.carrier.id);
    return this.carriers?.filter(c => !carrierAnswers.includes(c.id));
  }

  onAddContentItem(): void {
    this.addContentDialog.show();
    this.answerCreate.initializeForm(false, this.getAvailableCarriers());
  }

  initializeEditDialog(contentType: Carrier411AnswerDTO): void {
    this.answerEdit.initializeForm(contentType, false)
  }

  handleDenyAction(action: ContentTypeAction, contentType: Carrier411AnswerDTO): void {
    this.carrier411AdminService.getAnswerPreviousStep(contentType.id).subscribe((data) => {
      this.handlePreviousStatus(data, action, contentType);
    });
  }

  loadContentItems(): Promise<boolean> {
    return new Promise((resolve, reject) => {
      this.carrier411AdminService.getAnswers(this.question.id).subscribe((data) => {
        if (data.isSuccess) {
          this.setContentItems(data);
          this.carriersAvailable = this.getAvailableCarriers()?.length > 0;
          this.filterAnswers(this.route.snapshot.queryParams.answerId);
          resolve(true);
        } else {
          reject(data.errors);
        }
      });
    });
  }

  updateContentItem(data: ContentTypeStatusChangeDTO): void {
    const answer = data.contentType as Carrier411AnswerDTO;
    const model: Carrier411AnswerUpdateDTO = {
      text: answer.text,
      workflowStep: data.newStatus
    };
    this.carrier411AdminService.updateAnswer(data.contentType.id, model).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.onCloseModal(this.statusChangeDialog);
          this.loadContentItems();
        }
      });
    }, (err) => this.resultHandler.handle(err.error));
  }

  deleteContentItem(data: ContentTypeStatusChangeDTO): void {
    this.carrier411AdminService.deleteAnswer(data.contentType.id).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.onCloseModal(this.statusChangeDialog);
          this.loadContentItems();
        }
      });
    }, (err) => this.resultHandler.handle(err.error));
  }

  onConfirmToggleData($event: ContentTypeActionTakenDTO): void {
    const contentType = $event.contentType;

    this.carrier411AdminService.toggleAnswerVisibility(contentType.id).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.onCloseModal(this.contentTypeToggleDataDialog);
          this.loadContentItems();
        }
      });
    });
  }
}
