import { Injectable } from '@angular/core';
import { Resolve, RouterStateSnapshot, ActivatedRouteSnapshot } from '@angular/router';
import { Observable } from 'rxjs';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';
import { Carrier411CategoryDTO } from '../../shared/interfaces/carrier411-category.dto';
import { Carrier411AdminService } from '../services/carrier411-admin.service';

@Injectable()
export class CategoryResolver implements Resolve<ServiceResultDTO<Carrier411CategoryDTO>> {
  constructor(private carrier411AdminService: Carrier411AdminService) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<ServiceResultDTO<Carrier411CategoryDTO>> {
    const categoryId = Number(route.queryParamMap.get('categoryId'));
    return this.carrier411AdminService.getCategoryById(categoryId);
  }
}
