import { Carrier411AdminService } from 'src/app/carrier411/admin/services/carrier411-admin.service';
import { WorkflowStep } from './../../../../shared/constants/workflow-step.enum';
import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { Carrier411AnswerDTO } from "src/app/carrier411/shared/interfaces/carrier411-answer.dto";
import { Carrier411CategoryDTO } from "src/app/carrier411/shared/interfaces/carrier411-category.dto";
import { Carrier411QuestionDTO } from "src/app/carrier411/shared/interfaces/carrier411-question.dto";
import { Carrier411ResultDTO } from "src/app/carrier411/shared/interfaces/carrier411-result.dto";
import { Carrier411SubCategoryDTO } from "src/app/carrier411/shared/interfaces/carrier411-subcategory.dto";
import { ServiceResultDTO } from "src/app/core/interfaces/service-result/service-result.dto";
import { Carrier411PendingApprovalCountDTO } from '../../../interfaces/carrier411-pending-approval-counter.dto';
import { Carrier411RolesService } from 'src/app/carrier411/shared/services/carrier411-roles.service';
import { ApplicationUrls } from 'src/app/shared/constants/application-urls';

@Component({
  selector: 'pending-approval-management',
  templateUrl: './pending-approval-management.component.html',
  styleUrls: ['./pending-approval-management.component.scss']
})
export class Carrier411AdminPendingApprovalManagementComponent implements OnInit {
  public showCategory: boolean = true;
  public showSubCategory: boolean = false;
  public showQuestion: boolean = false;
  public showAnswer: boolean = false;
  public categories!: ServiceResultDTO<Carrier411ResultDTO<Carrier411CategoryDTO>[]>;
  public subcategories!: ServiceResultDTO<Carrier411ResultDTO<Carrier411SubCategoryDTO>[]>;
  public questions!: ServiceResultDTO<Carrier411ResultDTO<Carrier411QuestionDTO>[]>;
  public answers!: ServiceResultDTO<Carrier411ResultDTO<Carrier411AnswerDTO>[]>;
  public workflowSteps!: WorkflowStep[];
  public pendingApprovalCount!: Carrier411PendingApprovalCountDTO;

  returnUrl = ApplicationUrls.Carrier411Admin.path;

  constructor(
    route: ActivatedRoute,
    private carrier411RolesService: Carrier411RolesService,
    private carrier411AdminService: Carrier411AdminService
  ) {
    this.categories = route.snapshot.data["categories"] as ServiceResultDTO<Carrier411ResultDTO<Carrier411CategoryDTO>[]>;
    this.subcategories = route.snapshot.data["subcategories"] as ServiceResultDTO<Carrier411ResultDTO<Carrier411SubCategoryDTO>[]>;
    this.questions = route.snapshot.data["questions"] as ServiceResultDTO<Carrier411ResultDTO<Carrier411QuestionDTO>[]>;
    this.answers = route.snapshot.data["answers"] as ServiceResultDTO<Carrier411ResultDTO<Carrier411AnswerDTO>[]>;

    this.pendingApprovalCount = route.snapshot.data["pendingApprovalCount"].value as Carrier411PendingApprovalCountDTO;

    this.workflowSteps = this.carrier411RolesService.getAllowedWorkflowSteps();
  }

  ngOnInit() {
  }

  public getPendingApprovalCount(){
    this.carrier411AdminService.getPendingApprovalCount(this.workflowSteps).subscribe((data) => {
      this.pendingApprovalCount = data.value;
    });
  }

  public openCategories(){
    this.closeAllPendings();
    this.showCategory = true;
  }

  public openSubCategories(){
    this.closeAllPendings();
    this.showSubCategory = true;
  }

  public openQuestions(){
    this.closeAllPendings();
    this.showQuestion = true;
  }

  public openAnswers(){
    this.closeAllPendings();
    this.showAnswer = true;
  }

  closeAllPendings(){
    this.showCategory = false;
    this.showSubCategory = false;
    this.showQuestion = false;
    this.showAnswer = false;
  }

}
