import { Injectable } from "@angular/core";
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from "@angular/router";
import { Observable } from 'rxjs';
import { ServiceResultDTO } from "src/app/core/interfaces/service-result/service-result.dto";
import { Carrier411QuestionDTO } from "../../shared/interfaces/carrier411-question.dto";
import { Carrier411AdminService } from '../services/carrier411-admin.service';

@Injectable()
export class QuestionResolver implements Resolve<ServiceResultDTO<Carrier411QuestionDTO>> {
  constructor(private carrier411AdminService: Carrier411AdminService) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<ServiceResultDTO<Carrier411QuestionDTO>> {
    const questionId = Number(route.queryParamMap.get('questionId'));
    return this.carrier411AdminService.getQuestionById(questionId);
  }
}
