import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Carrier411BaseDTO } from 'src/app/carrier411/shared/interfaces/carrier411-base.dto';
import { Carrier411CategoryDTO } from 'src/app/carrier411/shared/interfaces/carrier411-category.dto';
import { ContentTypeActionTakenDTO } from '../../interfaces/content-type-action-taken.dto';
import { faArrowsRotate } from '@fortawesome/pro-solid-svg-icons';
import { faLock, faEyeSlash } from '@fortawesome/pro-regular-svg-icons';
import { WorkflowStep } from 'src/app/carrier411/shared/constants/workflow-step.enum';
import { Carrier411ResultDTO } from 'src/app/carrier411/shared/interfaces/carrier411-result.dto';
import { WorkflowStepUtils } from 'src/app/shared/utilities/workflow-step-utils';

@Component({
  selector: 'content-type-card',
  templateUrl: './content-type-card.component.html',
  styleUrls: ['./content-type-card.component.scss']
})
export class ContentTypeCardComponent implements OnInit {
  WorkflowStep = WorkflowStep;
  faArrowsRotate = faArrowsRotate;
  faLock = faLock;
  faEyeSlash = faEyeSlash;

  @Input("contentType")
  public contentType!: Carrier411ResultDTO<Carrier411BaseDTO>;
  @Input("hasPublishedVersion")
  public hasPublishedVersion: boolean = false;

  @Output("onAction")
  public onAction = new EventEmitter<ContentTypeActionTakenDTO>();

  constructor() { }

  ngOnInit(): void {
  }

  onTakeAction($event: ContentTypeActionTakenDTO) {
    if ($event != undefined) {
      this.onAction.emit($event);
    }
  }

  showHeaderHighlight = () => this.isHidden() || this.contentType.parentHidden || this.isRestricted() || this.contentType.parentRestricted;

  isHidden = () => this.contentType.item.isHidden;

  isRestricted = () => (this.contentType.item as Carrier411CategoryDTO)?.isRestricted;

  showPublishedIcon = () => this.hasPublishedVersion && !WorkflowStepUtils.compareEnumValue(this.contentType.item.workflowStep, WorkflowStep.Published);
}
