import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { ItemModel, MenuEventArgs } from '@syncfusion/ej2-angular-navigations';
import { Carrier411RolesService } from 'src/app/carrier411/shared/services/carrier411-roles.service';
import { SaveContentType } from '../../constants/save-content-type.enum';

@Component({
  selector: 'content-type-save-button',
  templateUrl: './content-type-save-button.component.html'
})
export class ContentTypeSaveButtonComponent implements OnInit {
  public items!: ItemModel[];

  @Output('onSave')
  public onSaveTrigger = new EventEmitter<SaveContentType>();

  constructor(
    private carrier411RolesService: Carrier411RolesService
  ) { }

  ngOnInit(): void {
    this.setupUserOptions();
  }

  setupUserOptions(): void {
    this.items = [
      { id: SaveContentType.Save.toString(), text: 'Save' },
      { id: SaveContentType.SaveAddMore.toString(), text: 'Save & Add More' }
    ];

    if (this.carrier411RolesService.isEditor()) {
      this.items = this.items.concat([
        { id: SaveContentType.PendingApproval.toString(), text: 'Pending Approval' },
        { id: SaveContentType.PendingApprovalAddMore.toString(), text: 'Pending Approval & Add More' }
      ])
    } else if (this.carrier411RolesService.isReviewer()) {
      this.items = this.items.concat([
        { id: SaveContentType.PendingPublish.toString(), text: 'Pending Publish' },
        { id: SaveContentType.PendingPublishAddMore.toString(), text: 'Pending Publish & Add More' }
      ])
    } else if (this.carrier411RolesService.isPublisher()) {
      this.items = this.items.concat([
        { id: SaveContentType.Publish.toString(), text: 'Publish' },
        { id: SaveContentType.PublishAddMore.toString(), text: 'Publish & Add More' }
      ])
    }
  }

  public onSelect(args?: MenuEventArgs) {
    if (args == undefined) {
      const step = SaveContentType[SaveContentType.Save.toString() as keyof typeof SaveContentType];
      this.onSaveTrigger.emit(step);
    } else {
      const step = SaveContentType[args.item.id as keyof typeof SaveContentType];
      this.onSaveTrigger.emit(step);
    }
  }
}
