import { AfterViewChecked, ChangeDetectorRef, Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { Carrier411AbstractDTO } from 'src/app/carrier411/shared/interfaces/carrier411-abstract.dto';
import { Carrier411CategoryDTO } from 'src/app/carrier411/shared/interfaces/carrier411-category.dto';
import { ContentItemOrderUpdateDTO } from '../../interfaces/content-item-order-update.dto';
import { faArrowsUpDownLeftRight } from '@fortawesome/pro-solid-svg-icons';
import { ListBoxComponent } from '@syncfusion/ej2-angular-dropdowns';

@Component({
  selector: 'content-type-manage-order',
  templateUrl: './content-type-manage-order.component.html',
  styleUrls: ['./content-type-manage-order.component.scss']
})
export class ContentTypeManageOrderComponent implements OnInit, AfterViewChecked {
  faArrowsUpDownLeftRight = faArrowsUpDownLeftRight;

  @Input('contentTypeLabel')
  public contentTypeLabel: string = '';

  @Output('onUpdate')
  public onUpdateTrigger = new EventEmitter<ContentItemOrderUpdateDTO[]>();
  @Output('onClose')
  public onCloseTrigger = new EventEmitter();

  public contentItems: Carrier411AbstractDTO[] | Carrier411CategoryDTO[] = [];

  @ViewChild('contentItemsListBox')
  private contentItemsListBox!: ListBoxComponent;

  constructor(private cd: ChangeDetectorRef) { }

  ngOnInit(): void {
  }

  /* RTE problem: Expression has changed after it was checked
   * http://www.syncfusion.com/forums/132152/rte-problem-expression-has-changed-after-it-was-checked?reply=ls7keM */
  ngAfterViewChecked() {
    this.cd.detectChanges();
  }

  initializeForm(contentItems: Carrier411AbstractDTO[]) {
    this.contentItems = contentItems;
  }

  onUpdate() {
    const newList = this.contentItemsListBox.getDataList() as any[];
    const newOrder = newList.map((c, index) => {
      return {
        id: c.id,
        order: index + 1
      }
    })

    this.onUpdateTrigger.emit(newOrder);
  }

  onClose() {
    this.onCloseTrigger.emit();
  }

  getFields(): any {
    if ('name' in this.contentItems[0]) return { text: 'name', value: 'id' };
    if ('text' in this.contentItems[0]) return { text: 'text', value: 'id' };
    return { text: 'name', value: 'id' };
  }

  getContentItemText(contentItem: any) {
    if ('name' in contentItem) return contentItem.name;
    if ('text' in contentItem) return contentItem.text;
    return '';
  }
}
