import { Component, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { Carrier411CategoryDTO } from 'src/app/carrier411/shared/interfaces/carrier411-category.dto';
import { Carrier411ResultDTO } from 'src/app/carrier411/shared/interfaces/carrier411-result.dto';
import { Carrier411SubCategoryDTO } from 'src/app/carrier411/shared/interfaces/carrier411-subcategory.dto';
import { ApplicationUrls } from 'src/app/shared/constants/application-urls';
import { faCheck } from '@fortawesome/pro-solid-svg-icons';
import { WorkflowStepUtils } from 'src/app/shared/utilities/workflow-step-utils';
import { Carrier411AdminNavBarItem } from '../../interfaces/carrier411-admin-navbar-item.dto';
import { Carrier411AdminNavbarService } from '../../services/carrier411-admin-navbar.service';
import { Carrier411QuestionDTO } from 'src/app/carrier411/shared/interfaces/carrier411-question.dto';
import { Carrier411AnswerDTO } from 'src/app/carrier411/shared/interfaces/carrier411-answer.dto';
import { ContentType } from 'src/app/carrier411/shared/constants/content-type.enum';
import { Carrier411AdminService } from '../../services/carrier411-admin.service';

@Component({
  selector: 'carrier411-admin-navbar',
  templateUrl: './carrier411-admin-navbar.component.html',
  styleUrls: ['./carrier411-admin-navbar.component.scss']
})
export class Carrier411AdminNavbarComponent implements OnInit {
  Carrier411ManagementURL = ApplicationUrls.Carrier411Admin.Carrier411Management.path;
  WorkflowStepUtils = WorkflowStepUtils;
  faCheck = faCheck;

  decodeURIComponent = decodeURIComponent;
  returnUrl = ApplicationUrls.Carrier411Admin.path;

  public items: any[] = [];

  private navCategoriesSubscription!: Subscription;
  private navSubCategoriesSubscription!: Subscription;
  private navQuestionsSubscription!: Subscription;
  private navAnswersSubscription!: Subscription;

  constructor(
    private carrier411AdminService: Carrier411AdminService,
    private navService: Carrier411AdminNavbarService
  ) { }

  ngOnInit(): void {
    this.navCategoriesSubscription = this.navService.navbarCategories$.subscribe(items => {
      this.setupCategoryTree(items);
    });
    this.navSubCategoriesSubscription = this.navService.navbarSubCategories$.subscribe(items => {
      this.setupSubCategoryTree(items);
    });
    this.navQuestionsSubscription = this.navService.navbarQuestions$.subscribe(items => {
      this.setupQuestionsTree(items);
    });
    this.navAnswersSubscription = this.navService.navbarAnswers$.subscribe(items => {
      this.setupAnswersTree(items);
    });
  }

  ngOnDestroy(): void {
    this.navService.injectCategories([]);
    this.navCategoriesSubscription.unsubscribe();

    this.navService.injectSubCategories(0, []);
    this.navSubCategoriesSubscription.unsubscribe();

    this.navService.injectQuestions(0, []);
    this.navQuestionsSubscription.unsubscribe();

    this.navService.injectAnswers(0, []);
    this.navAnswersSubscription.unsubscribe();
  }

  public itemExpanded(data: any) {
    const ids = data.id.toString().split('.');
    const contentType = this.getContentType(ids);
    if (contentType == ContentType.Category) {
      this.loadSubCategories(ids);
    } else if (contentType == ContentType.SubCategory) {
      this.loadQuestions(ids);
    } else if (contentType == ContentType.Question) {
      this.loadAnswers(ids);
    }
  }

  private getContentType(ids: string[]): ContentType | undefined {
    switch (ids.length) {
      case 1:
        return ContentType.Category;
      case 2:
        return ContentType.SubCategory;
      case 3:
        return ContentType.Question;
      case 4:
        return ContentType.Answer;
      default:
        return undefined;
    }
  }

  private setupEntireTree(dataSource: any[] | undefined): void {
    if (dataSource == undefined) return;

    this.items = dataSource;
  }

  private setupNode(
    currentData: any[],
    node: any[],
    items: any[] | undefined
  ): void {
    node[0].subChild = items?.map(i => {
      const subChild = node[0].subChild?.filter((sc: any) => sc.id == i.id);
      if (subChild && subChild.length) {
        i.subChild = subChild.flatMap((sc: any) => sc.subChild).filter((sc: any) => sc != null);
      }

      return i;
    });
    node[0].hasChildren = node[0].subChild != null && node[0].subChild.length;
    node[0].expanded = true;

    this.setupEntireTree(currentData);
  }

  /* Category */
  private setupCategoryTree(items: Carrier411ResultDTO<Carrier411CategoryDTO>[] | undefined): void {
    if (items == undefined) {
      this.items = [];
    }

    const formated = this.getFormatedCategories(items);
    this.setupEntireTree(formated);
  }

  private getFormatedCategories(items: Carrier411ResultDTO<Carrier411CategoryDTO>[] | undefined): any[] | undefined {
    if (items == undefined) return undefined;

    return items.map((c: Carrier411ResultDTO<Carrier411CategoryDTO>) => {
      return {
        id: c.item.id,
        name: c.item.name,
        hasChildren: c.childrenCount != null && c.childrenCount > 0,
        link: ApplicationUrls.Carrier411Admin.Carrier411Management.SubCategoryManagement.path,
        queryParams: {
          categoryId: c.item.id
        },
        hasPublishedVersion: c.item.hasPublishedVersion,
        workflowStep: c.item.workflowStep
      }
    });
  }

  /* Sub Category */
  private loadSubCategories(ids: string[]) {
    const categoryId = Number(ids[0]);
    if (this.items.some((c: any) => c.id == categoryId && (c.subChild == null || c.subChild.length == 0))) {
      this.carrier411AdminService.getSubCategories(categoryId).subscribe((data) => {
        if (data.isSuccess) {
          this.setupSubCategoryTree({
            parentId: categoryId,
            items: data.value
          })
        }
      });
    }
  }

  private setupSubCategoryTree(items: Carrier411AdminNavBarItem<Carrier411SubCategoryDTO> | undefined): void {
    if (items == undefined) return;

    const category = this.items.filter(c => c.id == items.parentId);
    if (category && category.length) {
      const formated = this.getFormatedSubCategories(items.parentId, items.items);
      this.setupNode(this.items, category, formated);
    }
  }

  private getFormatedSubCategories(parentId: number, items: Carrier411ResultDTO<Carrier411SubCategoryDTO>[] | undefined): any[] | undefined {
    if (items == undefined) return undefined;

    return items.map((sc: Carrier411ResultDTO<Carrier411SubCategoryDTO>) => {
      return {
        id: parentId + '.' + sc.item.id,
        name: sc.item.name,
        hasChildren: sc.childrenCount != null && sc.childrenCount > 0,
        link: ApplicationUrls.Carrier411Admin.Carrier411Management.QuestionManagement.path,
        queryParams: {
          subCategoryId: sc.item.id
        },
        hasPublishedVersion: sc.item.hasPublishedVersion,
        workflowStep: sc.item.workflowStep
      }
    });
  }

  /* Questions */
  private loadQuestions(ids: string[]) {
    const categoryId = Number(ids[0]);
    const subCategoryId = Number(ids[1]);
    const nodeId = `${categoryId}.${subCategoryId}`;

    if (this.items.some((c: any) =>
      c.subChild?.some((sc: any) => sc.id == nodeId && (sc.subChild == null || sc.subChild.length == 0))
    )) {
      this.carrier411AdminService.getQuestions(subCategoryId).subscribe((data) => {
        if (data.isSuccess) {
          this.setupQuestionsTree({
            parentId: subCategoryId,
            items: data.value
          })
        }
      });
    }
  }

  private setupQuestionsTree(items: Carrier411AdminNavBarItem<Carrier411QuestionDTO> | undefined) {
    if (items == undefined) return;

    const subCategory = this.items.flatMap(sc => sc.subChild)
      .filter(sc => sc?.id?.toString().endsWith(items.parentId));
    if (subCategory && subCategory.length) {
      const formated = this.getFormatedQuestions(subCategory[0].id, items.items);
      this.setupNode(this.items, subCategory, formated);
    }
  }

  private getFormatedQuestions(parentId: string, items: Carrier411ResultDTO<Carrier411QuestionDTO>[] | undefined): any[] | undefined {
    if (items == undefined) return undefined;

    return items.map((q: Carrier411ResultDTO<Carrier411QuestionDTO>) => {
      return {
        id: parentId + '.' + q.item.id,
        name: q.item.text,
        hasChildren: q.childrenCount != null && q.childrenCount > 0,
        link: ApplicationUrls.Carrier411Admin.Carrier411Management.AnswerManagement.path,
        queryParams: {
          questionId: q.item.id
        },
        hasPublishedVersion: q.item.hasPublishedVersion,
        workflowStep: q.item.workflowStep
      }
    });
  }

  /* Answers */
  private loadAnswers(ids: string[]) {
    const categoryId = Number(ids[0]);
    const subCategoryId = Number(ids[1]);
    const questionId = Number(ids[2]);
    const nodeId = `${categoryId}.${subCategoryId}.${questionId}`;

    const question = this.items.flatMap(c => c.subChild)
      .flatMap(sc => sc?.subChild)
      .filter(q => q?.id?.toString().endsWith(nodeId));

    if (question && question.length > 0 && (question[0].subChild == null || question[0].subChild.length == 0)) {
      this.carrier411AdminService.getAnswers(questionId).subscribe((data) => {
        if (data.isSuccess) {
          this.setupAnswersTree({
            parentId: questionId,
            items: data.value
          })
        }
      });
    }
  }

  private setupAnswersTree(items: Carrier411AdminNavBarItem<Carrier411AnswerDTO> | undefined) {
    if (items == undefined) return;

    const question = this.items.flatMap(c => c.subChild)
      .flatMap(sc => sc?.subChild)
      .filter(q => q?.id?.toString().endsWith(items.parentId));
    if (question && question.length) {
      const formated = this.getFormatedAnswers(items.parentId, question[0].id, items.items);
      this.setupNode(this.items, question, formated);
    }
  }

  private getFormatedAnswers(
    questionId: number,
    parentId: string,
    items: Carrier411ResultDTO<Carrier411AnswerDTO>[] | undefined
  ): any[] | undefined {
    if (items == undefined) return undefined;

    return items.map((a: Carrier411ResultDTO<Carrier411AnswerDTO>) => {
      return {
        id: parentId + '.' + a.item.id,
        name: a.item.carrier.name,
        link: ApplicationUrls.Carrier411Admin.Carrier411Management.AnswerManagement.path,
        queryParams: {
          questionId: questionId,
          answerId: a.item.id
        },
        hasPublishedVersion: a.item.hasPublishedVersion,
        workflowStep: a.item.workflowStep
      }
    });
  }
}
