import { Injectable } from '@angular/core';
import { Resolve, RouterStateSnapshot, ActivatedRouteSnapshot } from '@angular/router';
import { Observable } from 'rxjs';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';
import { Carrier411ResultDTO } from '../../shared/interfaces/carrier411-result.dto';
import { Carrier411SubCategoryDTO } from '../../shared/interfaces/carrier411-subcategory.dto';
import { Carrier411AdminService } from '../services/carrier411-admin.service';

@Injectable()
export class SubCategoriesAdminListResolver implements Resolve<ServiceResultDTO<Carrier411ResultDTO<Carrier411SubCategoryDTO>[]>> {

  constructor(private carrier411AdminService: Carrier411AdminService) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<ServiceResultDTO<Carrier411ResultDTO<Carrier411SubCategoryDTO>[]>> {
    const categoryId = Number(route.queryParamMap.get('categoryId'));
    return this.carrier411AdminService.getSubCategories(categoryId);
  }
}
