import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { Carrier411AnswerDTO } from '../../shared/interfaces/carrier411-answer.dto';
import { Carrier411CategoryDTO } from '../../shared/interfaces/carrier411-category.dto';
import { Carrier411QuestionDTO } from '../../shared/interfaces/carrier411-question.dto';
import { Carrier411ResultDTO } from '../../shared/interfaces/carrier411-result.dto';
import { Carrier411SubCategoryDTO } from '../../shared/interfaces/carrier411-subcategory.dto';
import { Carrier411AdminNavBarItem } from '../interfaces/carrier411-admin-navbar-item.dto';

@Injectable({
  providedIn: 'root'
})
export class Carrier411AdminNavbarService {
  private categoriesSource = new BehaviorSubject<Carrier411ResultDTO<Carrier411CategoryDTO>[] | undefined>(undefined);
  navbarCategories$ = this.categoriesSource.asObservable();

  private subCategoriesSource = new BehaviorSubject<Carrier411AdminNavBarItem<Carrier411SubCategoryDTO> | undefined>(undefined);
  navbarSubCategories$ = this.subCategoriesSource.asObservable();

  private questionsSource = new BehaviorSubject<Carrier411AdminNavBarItem<Carrier411QuestionDTO> | undefined>(undefined);
  navbarQuestions$ = this.questionsSource.asObservable();

  private answersSource = new BehaviorSubject<Carrier411AdminNavBarItem<Carrier411AnswerDTO> | undefined>(undefined);
  navbarAnswers$ = this.answersSource.asObservable();

  injectCategories(categories: Carrier411ResultDTO<Carrier411CategoryDTO>[]) {
    this.categoriesSource.next(categories);
  }

  injectSubCategories(categoryId: number, subCategories: Carrier411ResultDTO<Carrier411SubCategoryDTO>[]) {
    this.subCategoriesSource.next({
      parentId: categoryId,
      items: subCategories
    });
  }

  injectQuestions(subCategoryId: number, questions: Carrier411ResultDTO<Carrier411QuestionDTO>[]) {
    this.questionsSource.next({
      parentId: subCategoryId,
      items: questions
    });
  }

  injectAnswers(questionId: number, answers: Carrier411ResultDTO<Carrier411AnswerDTO>[], answerId?: number) {
    this.answersSource.next({
      parentId: questionId,
      items: answers,
      selectedId: answerId
    });
  }

  constructor() { }
}
