import { Carrier411SubCategoryDTO } from './../../../shared/interfaces/carrier411-subcategory.dto';
import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ResultHandlerService } from 'src/app/core/services/result.handler.service';
import { Carrier411AdminService } from '../../services/carrier411-admin.service';
import { ServiceResultDTO } from 'src/app/core/interfaces/service-result/service-result.dto';
import { ContentTypeActionTakenDTO } from '../../interfaces/content-type-action-taken.dto';
import { ContentTypeAction } from '../../constants/content-type-action.enum';
import { ContentTypeStatusChangeDTO } from '../../interfaces/content-type-status-change.dto';
import { ContentItemOrderUpdateDTO } from '../../interfaces/content-item-order-update.dto';
import { ApplicationUrls } from 'src/app/shared/constants/application-urls';
import { Carrier411ResultDTO } from 'src/app/carrier411/shared/interfaces/carrier411-result.dto';
import { Carrier411QuestionUpdateDTO } from '../../interfaces/carrier411-question-update.dto';
import { Carrier411QuestionDTO } from 'src/app/carrier411/shared/interfaces/carrier411-question.dto';
import { QuestionCreateComponent } from '../question-create/question-create.component';
import { QuestionEditComponent } from '../question-edit/question-edit.component';
import { ContentTypeManagementComponent } from '../content-type-management/content-type-management.component';
import { ContentTypeUtils } from '../../utilities/content-type-utils';
import { Carrier411AdminNavbarService } from '../../services/carrier411-admin-navbar.service';

@Component({
  selector: 'question-management',
  templateUrl: './question-management.component.html',
  styleUrls: ['./question-management.component.scss']
})
export class QuestionManagementComponent extends ContentTypeManagementComponent<Carrier411QuestionDTO> implements OnInit {
  AnswersLink = ApplicationUrls.Carrier411Admin.Carrier411Management.AnswerManagement.path;

  public subCategory!: Carrier411SubCategoryDTO;

  @ViewChild('questionCreate')
  public questionCreate!: QuestionCreateComponent;
  @ViewChild('questionEdit')
  public questionEdit!: QuestionEditComponent;

  constructor(
    private route: ActivatedRoute,
    protected resultHandler: ResultHandlerService,
    private carrier411AdminService: Carrier411AdminService,
    private navService: Carrier411AdminNavbarService
  ) {
    super(resultHandler);

    const data = route.snapshot.data["questions"] as ServiceResultDTO<Carrier411ResultDTO<Carrier411QuestionDTO>[]>;
    this.setContentItems(data);

    const subCategoryData = route.snapshot.data["subCategory"] as ServiceResultDTO<Carrier411SubCategoryDTO>;
    this.subCategory = subCategoryData.value;

    this.loadNavbar();
  }

  ngOnInit() {
    this.filters = ContentTypeUtils.getFilters(false);

    this.setupQuerySubscription();
  }

  loadNavbar() {
    if (this.subCategory != null) {
      this.navService.injectQuestions(this.subCategory.id, this.contentItems);
    }
  }

  setupQuerySubscription() {
    this.route.queryParams.subscribe(queryParams => {
      if (this.subCategory != null && this.subCategory.id != queryParams.subCategoryId) {
        this.loadSubCategory(queryParams.subCategoryId).then((success) => {
          if (success) {
            this.loadContentItems();
          }
        });
      }
    });
  }

  loadSubCategory(subCategoryId: number): Promise<boolean> {
    return new Promise((resolve, reject) => {
      this.carrier411AdminService.getSubCategoryById(subCategoryId).subscribe((data) => {
        if (data.isSuccess) {
          this.subCategory = data.value;
          resolve(true);
        } else {
          this.resultHandler.handle(data);
          reject(false);
        }
      });
    });
  }

  onAddContentItem() {
    this.addContentDialog.show();
    this.questionCreate.initializeForm();
  }

  onUpdateOrder(questions: ContentItemOrderUpdateDTO[]) {
    this.carrier411AdminService.reorderQuestions(questions).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.loadContentItems();
          this.manageOrderDialog.hide();
        }
      })
    });
  }

  initializeEditDialog(contentType: Carrier411QuestionDTO): void {
    this.questionEdit.initializeForm(contentType, false)
  }

  handleDenyAction(action: ContentTypeAction, contentType: Carrier411QuestionDTO) {
    this.carrier411AdminService.getQuestionPreviousStep(contentType.id).subscribe((data) => {
      this.handlePreviousStatus(data, action, contentType);
    });
  }

  loadContentItems() {
    this.carrier411AdminService.getQuestions(this.subCategory.id).subscribe((data) => {
      this.setContentItems(data);
    });
  }

  updateContentItem(data: ContentTypeStatusChangeDTO) {
    const question = data.contentType as Carrier411QuestionDTO;
    const model: Carrier411QuestionUpdateDTO = {
      workflowStep: data.newStatus,
      text: question.text
    };
    this.carrier411AdminService.updateQuestion(data.contentType.id, model).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.onCloseModal(this.statusChangeDialog);
          this.loadContentItems();
        }
      });
    }, (err) => this.resultHandler.handle(err.error));
  }

  deleteContentItem(data: ContentTypeStatusChangeDTO) {
    this.carrier411AdminService.deleteQuestion(data.contentType.id).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.onCloseModal(this.statusChangeDialog);
          this.loadContentItems();
        }
      });
    }, (err) => this.resultHandler.handle(err.error));
  }

  /* Toggle Data */
  onConfirmToggleData($event: ContentTypeActionTakenDTO) {
    const actionConverted = $event.action;
    const contentType = $event.contentType;

    switch (actionConverted) {
      case +ContentTypeAction.Hide:
      case +ContentTypeAction.Unhide:
        this.toggleVisibility(contentType as Carrier411QuestionDTO);
        break;
      default:
        this.onCloseModal(this.contentTypeToggleDataDialog);
        break;
    }
  }

  toggleVisibility(question: Carrier411QuestionDTO) {
    this.carrier411AdminService.toggleQuestionVisibility(question.id).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.onCloseModal(this.contentTypeToggleDataDialog);
          this.loadContentItems();
        }
      });
    });
  }
}
