import { Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { FormValidators } from '@syncfusion/ej2-angular-inputs';
import { CarrierDTO } from 'src/app/admin/carrier-management/interfaces/carrier.dto';
import { WorkflowStep } from 'src/app/carrier411/shared/constants/workflow-step.enum';
import { Carrier411AnswerCreateDTO } from 'src/app/carrier411/admin/interfaces/carrier411-answer-create.dto';
import { Carrier411QuestionDTO } from 'src/app/carrier411/shared/interfaces/carrier411-question.dto';
import { ResultHandlerService } from 'src/app/core/services/result.handler.service';
import { InputValidatorService } from 'src/app/shared/services/input-validator.service';
import { SaveContentType } from '../../constants/save-content-type.enum';
import { Carrier411AdminService } from '../../services/carrier411-admin.service';
import { ContentTypeUtils } from '../../utilities/content-type-utils';
import { RichTextEditorWithFileComponent } from 'src/app/shared/components/rich-text-editor-with-file/rich-text-editor-with-file.component';

@Component({
  selector: 'answer-create',
  templateUrl: './answer-create.component.html',
  styleUrls: ['./answer-create.component.scss']
})
export class AnswerCreateComponent implements OnInit {
  WorkflowStep = WorkflowStep;

  public form!: FormGroup;
  public carriers!: CarrierDTO[];

  @Input("question")
  public question!: Carrier411QuestionDTO;
  @Output('onClose')
  public onCloseTrigger = new EventEmitter<boolean>();

  @ViewChild('richText')
  public richText!: RichTextEditorWithFileComponent;

  private created: boolean = false;

  constructor(
    public inputValidatorService: InputValidatorService,
    private carrier411AdminService: Carrier411AdminService,
    private resultHandler: ResultHandlerService
  ) { }

  ngOnInit(): void {
    this.initializeForm();
  }

  initializeForm(created: boolean = false, carriers: CarrierDTO[] = []) {
    this.form = new FormGroup({
      carrierId: new FormControl('', [FormValidators.required]),
      text: new FormControl('', [FormValidators.required])
    });

    this.created = created;
    this.carriers = carriers;

    if (this.richText) {
      this.richText.initializeRichText('');
    }
  }

  onSave(option: SaveContentType) {
    this.form.markAllAsTouched();
    if (!this.form.valid) {
      return;
    }

    const model = this.form.getRawValue() as Carrier411AnswerCreateDTO;
    model.questionId = this.question.id;
    model.workflowStep = ContentTypeUtils.getWorkflowStep(option);

    this.saveAnswer(model, option);
  }

  onClose() {
    this.richText.richTextEditor.focusOut();
    this.onCloseTrigger.emit(this.created);
  }

  saveAnswer(model: Carrier411AnswerCreateDTO, step: SaveContentType) {
    this.richText.richTextEditor.focusOut();

    this.carrier411AdminService.createAnswer(model, this.richText.getFiles()).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          if (ContentTypeUtils.finishSaveAction(step)) {
            this.onCloseTrigger.emit(true);
          } else {
            this.initializeForm(true, this.carriers.filter(c => c.id != model.carrierId));
          }
        }
      });
    }, (err) => this.resultHandler.handle(err.error));
  }
}
