import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { WorkflowStep } from 'src/app/carrier411/shared/constants/workflow-step.enum';
import { Carrier411BaseDTO } from 'src/app/carrier411/shared/interfaces/carrier411-base.dto';
import { WorkflowStepUtils } from 'src/app/shared/utilities/workflow-step-utils';
import { faCheck, faChevronDown, faChevronRight } from '@fortawesome/pro-solid-svg-icons';
import { animate, style, transition, trigger } from '@angular/animations';

@Component({
  selector: 'carrier411-admin-navbar-levels',
  templateUrl: './carrier411-admin-navbar-levels.component.html',
  styleUrls: ['./carrier411-admin-navbar-levels.component.scss'],
  animations: [
    trigger('slide', [
      transition(":enter", [
        style({ transform: "translateY(100%)", opacity: 0 }),
        animate('200ms', style({ transform: "translateY(-100%)", opacity: 1 }))
      ]),
      transition(':leave', [
        style({ transform: 'translateX(0)', opacity: 1 }),
        animate('200ms', style({ transform: 'translateX(100%)', opacity: 0 }))
      ])
    ])
  ]
})
export class Carrier411AdminNavbarLevelsComponent implements OnInit {
  WorkflowStepUtils = WorkflowStepUtils;
  faCheck = faCheck;
  faChevronDown = faChevronDown;
  faChevronRight = faChevronRight

  @Input('items')
  public items: any[] = [];
  @Output('expanded')
  public expandedTrigger = new EventEmitter<any>();

  constructor() { }

  ngOnInit(): void {
  }

  public showNonPublishedStatus = (data: Carrier411BaseDTO): boolean =>
    !WorkflowStepUtils.compareEnumValue(data.workflowStep, WorkflowStep.Published);

  public getPublishedDescription = (item: Carrier411BaseDTO): string =>
    item.hasPublishedVersion ? WorkflowStepUtils.getDescription(WorkflowStep.Published)! : 'Not Yet Published';

  public getWorkflowStep = (item: Carrier411BaseDTO): string =>
    item.hasPublishedVersion ? WorkflowStep[WorkflowStep.Published].toLowerCase() : WorkflowStep[WorkflowStep.Draft].toLowerCase();

  public isExpanded = (data: any): boolean => data.hasChildren && data.expanded;

  public itemExpanded(data: any) {
    data.expanded = true;
    this.expandedTrigger.emit(data);
  }

  public itemCollapsed(data: any) {
    data.expanded = false;
    if (data.subChild) {
      data.subChild.forEach((sb: any) => {
        this.itemCollapsed(sb);
      })
    }
  }
}
