import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { FormValidators } from '@syncfusion/ej2-angular-inputs';
import { AccountService } from 'src/app/authentication/services/account.service';
import { ResultHandlerService } from 'src/app/core/services/result.handler.service';
import { InputValidatorService } from 'src/app/shared/services/input-validator.service';
import { Carrier411SubCategoryUpdateDTO } from '../../interfaces/carrier411-subcategory-update.dto';
import { Carrier411SubCategoryDTO } from '../../../shared/interfaces/carrier411-subcategory.dto';
import { Carrier411AdminService } from '../../services/carrier411-admin.service';
import { Carrier411AdminMessages } from '../../utilities/carrier411-admin-messages';

@Component({
  selector: 'subcategory-edit',
  templateUrl: './subcategory-edit.component.html'
})
export class SubCategoryEditComponent implements OnInit {

  @Input()
  public categoryId?: number;

  @Input()
  public categoryName?: string;

  @Output('onClose')
  public onCloseTrigger = new EventEmitter<boolean>();

  public form!: FormGroup;
  public userWarningMessage: string = '';

  private subCategory?: Carrier411SubCategoryDTO;
  private edited: boolean = false;

  constructor(
    public inputValidatorService: InputValidatorService,
    private carrier411AdminService: Carrier411AdminService,
    private accountService: AccountService,
    private resultHandler: ResultHandlerService
  ) { }

  ngOnInit(): void {
    this.initializeForm(undefined, false);
  }

  initializeForm(subCategory?: Carrier411SubCategoryDTO, edited: boolean = false) {
    this.subCategory = subCategory;
    this.edited = edited;

    this.form = new FormGroup({
      name: new FormControl(subCategory?.name, [FormValidators.required])
    });

    this.setupUserWarning();
  }

  onSave() {
    this.form.markAllAsTouched();
    if (!this.form.valid) {
      return;
    }

    const model = this.form.getRawValue() as Carrier411SubCategoryUpdateDTO;
    model.workflowStep = this.subCategory!.workflowStep;

    this.updateSubCategory(model);
  }

  onClose() {
    this.onCloseTrigger.emit(this.edited);
  }

  updateSubCategory(model: Carrier411SubCategoryUpdateDTO) {
    this.carrier411AdminService.updateSubCategory(this.subCategory!.id, model).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.onCloseTrigger.emit(true);
        }
      });
    }, (err) => this.resultHandler.handle(err.error));
  }

  setupUserWarning() {
    if (this.hasDifferentOwner()) {
      this.userWarningMessage = Carrier411AdminMessages.getUserEditorWarningMessage(this.subCategory?.lastModifiedByName);
    }
    else {
      this.userWarningMessage = '';
    }
  }

  hasDifferentOwner = (): boolean => this.subCategory?.lastModifiedById != this.accountService.getUser()?.id;
}
