import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { WorkflowStep } from 'src/app/carrier411/shared/constants/workflow-step.enum';
import { faCheck, faClose, faPencil } from '@fortawesome/pro-regular-svg-icons';
import { faEllipsis } from '@fortawesome/pro-solid-svg-icons';
import { Carrier411BaseDTO } from 'src/app/carrier411/shared/interfaces/carrier411-base.dto';
import { ItemModel, MenuEventArgs } from '@syncfusion/ej2-angular-navigations';
import { ContentTypeAction } from '../../constants/content-type-action.enum';
import { Carrier411RolesService } from 'src/app/carrier411/shared/services/carrier411-roles.service';
import { ContentTypeActionTakenDTO } from '../../interfaces/content-type-action-taken.dto';

@Component({
  selector: 'content-type-actions',
  templateUrl: './content-type-actions.component.html',
  styleUrls: ['./content-type-actions.component.scss']
})
export class ContentTypeActionsComponent implements OnInit {
  otherIcon = faEllipsis;

  @Input("contentType")
  public contentType!: Carrier411BaseDTO;
  @Input("hasPublishedVersion")
  public hasPublishedVersion: boolean = false;

  @Output("onAction")
  public onAction = new EventEmitter<ContentTypeActionTakenDTO>();

  public mainActions: any[] = [];
  public otherActions: ItemModel[] = [];
  public hasAction: boolean = false;

  constructor(
    private carrier411RolesService: Carrier411RolesService
  ) { }

  ngOnInit(): void {
    this.setupActions();
  }

  onTakeAction(action: string | undefined) {
    if (action != undefined) {
      const actionType = ContentTypeAction[action as keyof typeof ContentTypeAction];
      this.onAction.emit({ action: actionType, contentType: this.contentType });
    }
  }

  select(args: MenuEventArgs) {
    this.onTakeAction(args.item.id);
  }

  setupActions() {
    this.mainActions = [];
    this.otherActions = [];

    const isReviewer = this.carrier411RolesService.isReviewer();
    const isPublisher = this.carrier411RolesService.isPublisher();

    switch (+WorkflowStep[this.contentType.workflowStep]) {
      case WorkflowStep.Draft:
        this.mainActions.push(this.getApproveButton());
        this.mainActions.push(this.getEditButton());
        this.otherActions.push(this.getDeleteButton());
        this.handleHideButtons(isPublisher);
        this.handleRestrictAccessButtons(isPublisher);
        break;
      case WorkflowStep.PendingApproval:
        if (isReviewer || isPublisher) {
          this.mainActions = this.getAllMainActions();
        }

        if (!this.hasPublishedVersion) {
          this.otherActions.push(isPublisher ? this.getDeleteButton() : this.getRequestDeleteButton());
        }

        this.handleHideButtons(isPublisher);
        this.handleRestrictAccessButtons(isPublisher);
        break;
      case WorkflowStep.PendingPublish:
        if (isPublisher) {
          this.mainActions = this.getAllMainActions();
        }

        if (!this.hasPublishedVersion) {
          this.otherActions.push(isPublisher ? this.getDeleteButton() : this.getRequestDeleteButton());
        }

        this.handleHideButtons(isPublisher);
        this.handleRestrictAccessButtons(isPublisher);
        break;
      case WorkflowStep.Published:
        this.mainActions.push(this.getEditButton());
        this.otherActions.push(isPublisher ? this.getDeleteButton() : this.getRequestDeleteButton());
        this.handleHideButtons(isPublisher);
        this.handleRestrictAccessButtons(isPublisher);
        break;
      case WorkflowStep.DeleteRequested:
        if (isReviewer || isPublisher) {
          this.mainActions.push(this.getApproveButton());
          this.mainActions.push(this.getDenyButton());
        }
        break;
      case WorkflowStep.PendingDelete:
        if (isPublisher) {
          this.mainActions.push(this.getApproveButton());
          this.mainActions.push(this.getDenyButton());
        }
        break;
      default:
        break;
    }

    this.hasAction = this.mainActions.length > 0 || this.otherActions.length > 0;
  }

  /* Main Actions */
  getApproveButton = () => ({ id: ContentTypeAction.Approve.toString(), icon: faCheck, class: 'approve-button' });
  getDenyButton = () => ({ id: ContentTypeAction.Deny.toString(), icon: faClose, class: 'deny-button' });
  getEditButton = () => ({ id: ContentTypeAction.Edit.toString(), icon: faPencil, class: 'edit-button' });
  getAllMainActions = () => [this.getApproveButton(), this.getDenyButton(), this.getEditButton()];

  /* Other Actions */
  getDeleteButton = (): ItemModel => ({ id: ContentTypeAction.Delete.toString(), text: 'Delete' });
  getRequestDeleteButton = (): ItemModel => ({ id: ContentTypeAction.Delete.toString(), text: 'Request Delete' });
  getHideButton = () => ({ id: ContentTypeAction.Hide.toString(), text: 'Hide' });
  getUnhideButton = () => ({ id: ContentTypeAction.Unhide.toString(), text: 'Unhide' });
  getRestrictAccessButton = () => ({ id: ContentTypeAction.RestrictAccess.toString(), text: 'Restrict Access' });
  getUnrestrictAccessButton = () => ({ id: ContentTypeAction.UnrestrictAccess.toString(), text: 'Unrestrict Access' });

  handleHideButtons(isPublisher: boolean) {
    if (isPublisher) {
      if (this.contentType.isHidden) {
        this.otherActions.push(this.getUnhideButton());
      } else {
        this.otherActions.push(this.getHideButton());
      }
    }
  }

  handleRestrictAccessButtons(isPublisher: boolean) {
    if (isPublisher && 'isRestricted' in this.contentType) {
      if ((this.contentType as any).isRestricted) {
        this.otherActions.push(this.getUnrestrictAccessButton());
      } else {
        this.otherActions.push(this.getRestrictAccessButton());
      }
    }
  }
}
