import { PendingQuestionsListResolver } from './resolvers/pending-questions-list.resolver';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { RoleGuard } from 'src/app/authentication/guards/role.guard';
import { Roles } from 'src/app/core/constants/role.constants';

// Components
import { AnswerManagementComponent } from './components/answer-management/answer-management.component';
import { Carrier411AdminLandingComponent } from './components/carrier411-admin-landing/carrier411-admin-landing.component';
import { Carrier411AdminLandingPageContentComponent } from './components/landing-page-content-management/landing-page-content.component';
import { Carrier411AdminPendingApprovalManagementComponent } from './components/pending-approval-management/components/pending-approval-management.component';
import { Carrier411ManagementComponent } from './components/carrier411-management/carrier411-management.component';
import { CategoryManagementComponent } from './components/category-management/category-management.component';
import { QuestionManagementComponent } from './components/question-management/question-management.component';
import { SubCategoryManagementComponent } from './components/subcategory-management/subcategory-management.component';

// Resolvers
import { AnswersListResolver } from './resolvers/answers-list.resolver';
import { Carrier411MessagesResolver } from '../shared/resolvers/carrier411-messages.resolver';
import { CategoriesListAdminResolver } from './resolvers/categories-list-admin.resolver';
import { CategoryResolver } from './resolvers/category.resolver';
import { QuestionsListResolver } from './resolvers/questions-list.resolver';
import { QuestionResolver } from './resolvers/question.resolver';
import { SubCategoriesAdminListResolver } from './resolvers/sub-categories-list-admin.resolver';
import { SubCategoryResolver } from './resolvers/sub-category.resolver';
import { PendingCategoriesListResolver } from './resolvers/pending-categories-list.resolver';
import { PendingSubCategoriesListResolver } from './resolvers/pending-subcategories-list.resolver';
import { PendingAnswersListResolver } from './resolvers/pending-answers-list.resolver';
import { PendingApprovalCountListResolver } from './resolvers/pending-approval-counter.resolver';

const childRoutes: Routes = [
  { path: '', component: Carrier411AdminLandingComponent },
  {
    path: 'question-management',
    component: Carrier411ManagementComponent,
    children: [
      {
        path: '',
        component: CategoryManagementComponent,
        resolve: { categories: CategoriesListAdminResolver }
      },
      {
        path: 'subcategories',
        component: SubCategoryManagementComponent,
        resolve: { category: CategoryResolver, subCategories: SubCategoriesAdminListResolver }
      },
      {
        path: 'questions',
        component: QuestionManagementComponent,
        resolve: { subCategory: SubCategoryResolver, questions: QuestionsListResolver }
      },
      {
        path: 'answers',
        component: AnswerManagementComponent,
        resolve: { question: QuestionResolver, answers: AnswersListResolver }
      }
    ]
  },
  {
    path: 'landing-page-content-management',
    component: Carrier411AdminLandingPageContentComponent,
    resolve: { messages: Carrier411MessagesResolver }
  },
  {
    path: 'pending-approval-management',
    component: Carrier411AdminPendingApprovalManagementComponent,
    canActivate: [RoleGuard],
    data: { roles: [Roles.Carrier411.Reviewer, Roles.Carrier411.Publisher] },
    resolve: {
      pendingApprovalCount: PendingApprovalCountListResolver,
      categories: PendingCategoriesListResolver,
      subcategories: PendingSubCategoriesListResolver,
      questions: PendingQuestionsListResolver,
      answers: PendingAnswersListResolver
    }
  }
]

@NgModule({
  declarations: [],
  imports: [RouterModule.forChild(childRoutes)],
  exports: [RouterModule]
})

export class Carrier411AdminRoutingModule { }
